import { Component, OnInit } from '@angular/core';
import {Store} from '@ngrx/store';
import {UnifiedDashboardService} from '../../../services/unified-dashboard.service';
import {go} from '@ngrx/router-store';
import {LocalStorageService} from 'angular-2-local-storage';
import {TripDetailSocketService} from "../../../services/trip.socket.service";
import {Angulartics2Mixpanel} from "angulartics2";

@Component({
  selector: 'app-unified-vehicle-list',
  templateUrl: './unified-vehicle-list.component.html',
  styleUrls: ['./unified-vehicle-list.component.css']
})
export class UnifiedVehicleListComponent implements OnInit {



  public unifiedTripDetailList: any;
  public unSub_TripStatusDetails =null;
  public view ='';
  // public unSub_TripStatusDeatils =null;



  //trip variables
  public isSpinner: boolean = false;
  public unSub_customerDetails = null;
  public unSub_ETA_DistanceStore = null;
  public unSub_updateTripData = null;
  public unSub_TripStatus = null;
  public tripStatusList: any = [];
  // public previousSelectedVehicle: any = {_isShowDetails: false};

  constructor(private tripSocketObj: TripDetailSocketService,private _store:Store<any>,public unifedDashboardService:UnifiedDashboardService,private localStorageService: LocalStorageService,public _angularitics:Angulartics2Mixpanel) {
    this._store.dispatch({type: "TITLE",payload:'Unified Dashboard'});
    this.view=document['view'];
    console.log(this.view);
    this.isSpinner= true;
    // this.unifedDashboardService.getTripStatus.emit();
    // this.unSub_TripStatusDetails = _store.select('unifiedTripStatusList').subscribe(value => {
    //   console.log('unified trip status list');
    //   console.log(value);
    //   if (value == null) {
    //     return;
    //   }
    //
    //   this.unifiedTripDetailList=value;
    //   console.log(value);
    //
    //   for(let i=0; i< this.unifiedTripDetailList.length; i++){
    //     if(this.unifiedTripDetailList[i].vtsDuration != null || this.unifiedTripDetailList[i].vtsDuration != undefined) {
    //       this.unifiedTripDetailList[i]._vtsDuration = this.msToTime(this.unifiedTripDetailList[i].vtsDuration);
    //     }
    //     if(this.unifiedTripDetailList[i].gpsStateDuration != null || this.unifiedTripDetailList[i].gpsStateDuration != undefined) {
    //       this.unifiedTripDetailList[i]._gpsStateDuration = this.msToTime(this.unifiedTripDetailList[i].gpsStateDuration);
    //     }
    //
    //   }
    // });


    this.unSub_TripStatus = _store.select('tripStatusList').subscribe(value => {
      console.log(value);
      if (value == null)
        return;

      this.isSpinner = false;

      this.tripStatusList = JSON.parse(JSON.stringify(value));
      // create new array
      console.log(this.tripStatusList);
      this.updateList();
      // console.log(this.vehicleTripCount);
      // console.log(this.gpsState);
      // console.log(this.doughnutChartData);
    });


    this.unSub_customerDetails = _store.select('customerDetails').subscribe(value => {
      // console.log(value);
      if (value == null) {
        return;
      }
      console.log(value);
      this.tripSocketObj.getTripList();
    });

    //dispatch data from trip-socket.service
    this.unSub_updateTripData = _store.select('updateTripData').subscribe(res => {
      let value =res;

      // console.log(value);
      if (value == null || this.tripStatusList == null) {
        return;
      }

      for (let i = 0; i < this.tripStatusList.length; i++) {
        if(this.tripStatusList[i]['vts'] != value['vts'] && this.tripStatusList[i]['uuid'] == value['vehicleId']){
          // this.merge(this.tripStatusList[i], value);
          console.log(this.tripStatusList[i]);
          this.updateList();
        }
      }
    });


    this.unSub_ETA_DistanceStore = _store.select("etaListDistanceStore").subscribe(data=> {
      if (data == null)
        return;

      // console.log(data);
      let distance = (data['value'][0]['distance']['value'] / 1000).toFixed(2) + " km"
      // console.log(data);
      // console.log(this.tripStatusList);
      let ind = data['index'];
      if (this.tripStatusList.length > ind) {
        // console.log(this.tripStatusList[ind]['distanceToGo'])

        this.tripStatusList[ind].distanceToGo = distance;
        this.tripStatusList[ind]['eta'] = this.msToTime(data['value'][0]['duration']['value'] * 1000);
        this.tripStatusList[ind]['timeValue'] = data['value'][0]['duration']['value'];

        // console.log(this.tripStatusList);
      }
    });
  }

  ngOnInit() {
  }

  ngOnDestroy()
  {

    if (typeof this.unSub_TripStatusDetails != 'undefined' && this.unSub_TripStatusDetails != null && this.unSub_TripStatusDetails != undefined)
      this.unSub_TripStatusDetails.unsubscribe();

    this.tripSocketObj.disconnectTripListSocket();
    if (typeof this.unSub_TripStatus != 'undefined' && this.unSub_TripStatus != null && this.unSub_TripStatus != undefined)
      this.unSub_TripStatus.unsubscribe();

    if (typeof this.unSub_ETA_DistanceStore != 'undefined' && this.unSub_ETA_DistanceStore != null && this.unSub_ETA_DistanceStore != undefined)
      this.unSub_ETA_DistanceStore.unsubscribe();

    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined)
      this.unSub_customerDetails.unsubscribe();

    if (typeof this.unSub_updateTripData != 'undefined' && this.unSub_updateTripData != null && this.unSub_updateTripData != undefined)
      this.unSub_updateTripData.unsubscribe();


  }

  goToLoactionHisView(vehicle){
    console.log(vehicle);
    console.log(vehicle.vts);
    this._store.dispatch({type: "VEHICLE_FOR_LOCATION_HISTORY", payload: vehicle});

      if (this.view != 'web') {
        console.log(vehicle);
        if(vehicle.vts == 'WaitingForLoad') {
          this._store.dispatch({type: "LOCATION_HISTORY", payload: null}); // clear previous records
          this.localStorageService.set('SELECTED-LOCATION-HIS-VEHICLE', vehicle); // put value in local storage
          this._store.dispatch(go(['/vtsdashboard/locationHistory']));
          this._store.dispatch({type: "GET_TIMELINE", payload: null});
        }else{
          this.showDetails(vehicle);
        }
      }
      else {
        if(vehicle.vts == 'WaitingForLoad') {
          console.log(this.view);
          let show = {
            "isShow": true
          }
          this._store.dispatch({type: "SHOW_LOCATION_HISTORY", payload: show});
          this._store.dispatch({type: "LOCATION_HISTORY", payload: null}); // clear previous records
          this.localStorageService.set('SELECTED-LOCATION-HIS-VEHICLE', vehicle); // put value in local storage
          this._store.dispatch({type: "GET_TIMELINE", payload: {callFrom: 'web'}});
        }else{
          this.showDetails(vehicle);
        }
      }
  }

  msToTime(duration) {
    let x = duration / 1000;
    let seconds = Math.floor(x % 60);
    x /= 60;
    let minutes = Math.floor(x % 60);
    x /= 60;
    let hours = Math.floor(x % 24);
    x /= 24;
    let days = Math.floor(x);

    // console.log(days);
    let totalDuration='';

    if(days>0){
      totalDuration=days+"d ";
    }
    if(hours>0){
      totalDuration=totalDuration+hours+"hr ";
    }

    if(days >= 1){
      return totalDuration;
    }

    if(minutes>0){
      return totalDuration=totalDuration+minutes+"min";
    }
    if(seconds>0 && totalDuration == ''){
      totalDuration=totalDuration+seconds+" sec";
    }

    if(totalDuration==''){
      return '0 sec';
    }else {
      return totalDuration;
    }

  }//close methods msToTime


  showDetails(vehicle) {
    console.log(vehicle)
    if(this.view =='web'){
      this._angularitics.eventTrack('TripView_Item_Clicked', vehicle);
      let obj = JSON.parse(JSON.stringify(vehicle));
      this.localStorageService.set("CURRENT_TRIP_VEHICLE", obj);
      this._store.dispatch({type: "VEHICLE_TRIP_DETAILS", payload: null});
    }else {
      console.log(vehicle);
      let obj = JSON.parse(JSON.stringify(vehicle));
      this.localStorageService.set("CURRENT_TRIP_VEHICLE", obj);
      this._angularitics.eventTrack('TripView_Item_Clicked', vehicle); // records mixpanel event for click on trip status list
      this._store.dispatch({type: "VEHICLE_TRIP_DETAILS", payload: null});
      this._store.dispatch(go(['vtsdashboard/trip/status/details']));
    }
  }

  closeDetails() {
    // this.previousSelectedVehicle['_isShowDetails'] = false;
  }

  // merge(original, delta) {
  //   // return Object.assign(original, delta);
  //   for (var attrname in delta) {
  //     original[attrname] = delta[attrname];
  //   }
  // }

  public updateList() {
    for (let i = 0; i < this.tripStatusList.length; i++) {
      this.tripStatusList[i]._vtsDuration = this.msToTime(this.tripStatusList[i].vtsDuration);
    }

  }

  to12HourFormat(date) {
    let hours = ((date.getHours() + 11) % 12 + 1);
    let minutes = date.getMinutes();
    let meridian = (date.getHours() >= 12) ? 'PM' : 'AM';

    return hours+":"+minutes+" "+meridian;
  }

  getMonthName(month)
  {
    return ["January","February","March","April","May","June","July","August","September", "October","November","December"][parseInt(month)]
  }


}




