import { Component, OnInit } from '@angular/core';
import {Store} from "@ngrx/store";
import {go} from "@ngrx/router-store";

import { TataFleetmanService } from '../../../services/tata-fleetman-service';
import { paths } from 'environments/environment.prod';
import {MdSnackBar, MdSnackBarConfig} from "@angular/material";

@Component({
  selector: 'app-tata-fleetman-form',
  templateUrl: './tata-fleetman-form.component.html',
  styleUrls: ['./tata-fleetman-form.component.css']
})
export class TataFleetmanFormComponent implements OnInit {
  
  public unSub_tpiVehicleList = null;
  public isSpinner= false;
  public view ='';
  public loginObj ={
    "username" : "",
    "password" : "",
   }

   ngOnDestroy() {
    
    if (typeof this.unSub_tpiVehicleList != 'undefined' && this.unSub_tpiVehicleList != null && this.unSub_tpiVehicleList != undefined){
      console.log("unSub tpi VehicleList");
      this.unSub_tpiVehicleList.unsubscribe();
    }
  }

  ngOnInit() {
  }

  constructor(private _store:Store<any>, private tataFleetmanService:TataFleetmanService,public snackBar: MdSnackBar ) {

    //store subscribe here 
    //this._store.dispatch({type: "TPI_VEHICLE_LIST",payload:'Tpi Identified Vehicles'}); // set the header name
    console.log('TPI integrated vehicles');

    this.view=document['view'];

    this.unSub_tpiVehicleList = _store.select('tpiVehicleList').subscribe(value => {
      //console.log(value);
      if(value==null)
      {
        return;
      }
      
      if(value['error'] == '') {
        this.isSpinner=false;
        console.log(value);     
        this._store.dispatch(go(['/vtsdashboard/tpiIdentifiedVehicles']));
      }
        else {
          this.isSpinner=false;
          this.openSnackBar(" UNAUTHORISED ACCESS ");
          console.log(value['errorMsg']);
        
      } 
    });
   

  }

  
  goToTpi(){
    this._store.dispatch(go(['/vtsdashboard/thirdPartyIntegration']));
  }

  goToTpiIdentifiedVehicles(){


      if(this.loginObj.username=='' || this.loginObj.username== undefined){
        console.log('Please input customer Id');
        this.openSnackBar("Please Input CustomerID");
        return false;
    }
      else if(this.loginObj.password ==''|| this.loginObj.password ==undefined){
        console.log('Password required');
        this.openSnackBar("Password Required");
        return false;
    }
      else {
        this.isSpinner= true;
        this.tataFleetmanService.getTpiVehicleList.emit(this.loginObj);
    
 }
 
 
}
  openSnackBar(message: string) {
    this.snackBar.open(message , '', <MdSnackBarConfig>{duration :  3000});
  }


}


