import { Component, OnInit } from '@angular/core';
import {Store} from "@ngrx/store";
import {go} from "@ngrx/router-store";
import {VehicleManagementService} from "../../../services/vehicle-management-services";
import { MdSnackBar, MdSnackBarConfig } from "@angular/material";
import { DriverManagementService } from "../../../services/driver-registry-service";
import {LocalStorageService} from "angular-2-local-storage";



@Component({
  selector: 'app-manage-vehicle-list',
  templateUrl: './manage-vehicle-list.component.html',
  styleUrls: ['./manage-vehicle-list.component.css']
})
export class ManageVehicleListComponent implements OnInit {

  public isShowAddNewVehicle:boolean;
  public view ='';

  public allDriverList:any[]=[]
  public vehicleTypeList:any[] =[
    'Tata 407 2.5MT',
    'Canter/Swaraj Mazda 3.5MT',
    'LPT 709 4.5MT',
    'LPT 1109 7.5MT',
    'Taurus 15MT',
    'Containerized Vehicle 7 MT',
    'Containerized Vehicle 14 MT',
    'Open Truck 9MT',
    'JCB 8MT',
    'High Bed Trailer Double Axle Upto 22 MT',
    'Trailer Triple Axle Upto 27 MT',
    'Horse for Mobile Unit upto 49 MT',
    'Low Bed Trailer for ODC Upto 49 MT',
    'Volvo/Mann/4923/4921 49 MT',
    'Volvo/Mann/AMW 150 MT',
  ];

  public driverName:any;
  public mobNo:any;
  public driverId:any;
  public isShowDriver:boolean=false;
  public isRemoveIcon:boolean=false;

  public vehicleStatusList:any[] =["Own Vehicle","Partner Vehicle"];
  public gpsVendorList:any[] =["Fretron","MapMyIndia", "Others"];

  public vehicleNumber:string ='';
  public vehicleType:string ='';
  public driverMobNo:string ='';
  public vtsDeviceIdVehicle:string ='';
  public vehicleStatus:string ='';
  public orgId:string ='';
  public shareOrgId:string ='';
  public gpsVendor:string ='';
  public vehiclesByOrgList :any = [];
  public selectedVehicles :any = [];
  public vehicleSharedWithOrgList :any = [];
  public sharedOrganisationDetails :any;
  public selectedVehicleForCancel :any;
  public selectedVehicleForDelete :any;
  public customerDetails :any;
  public isSelcetedForShare:boolean=false;
  public isShowShareVehicle:boolean=false;
  public isShowOrgConfirm:boolean=false;
  public isShowSharedDetails:boolean=false;
  public isShowConfirmDelete:boolean=false;

  private unSub_vehiclesByOrgList: any = null;
  private unSub_customerDetails: any = null;
  private unSub_refreshList: any = null;
  private unSub_addNewVehicle: any = null;
  private unSub_shareVehicleResp: any = null;
  private unSub_sharedOrgDetails: any = null;
  private unSub_sharedWithOrgList: any = null;
  private unSub_cancelSharing: any = null;
  private unSub_allDeviceList:any=null;
  private unSub_deleteVehicleFromOrgResponse:any=null;
  public isShowAddDriver:boolean=false;
  public isGod:boolean=false;




  constructor(private _store:Store<any>,public vehicleMgmntService: VehicleManagementService,public snackBar: MdSnackBar,private driverService:DriverManagementService,public localStorageService: LocalStorageService) {
    this.view=document['view'];

    this.unSub_customerDetails = _store.select('customerDetails').subscribe(value => {
      console.log(value);
      if(value==null){
        return;
      }

      if(value['customer']['isGod'])
      {
        this.isGod=true;
      }

      this.orgId=value['customer']['orgId'];
      this.vehicleMgmntService.getAllVehiclesByOrg.emit(this.orgId);

      setTimeout(() =>{
        this.vehicleMgmntService.getAllVehiclesByOrg.emit(this.orgId);
      },1000);

      // setTimeout(() =>{
      //   this.vehicleMgmntService.getAllVehiclesByOrg.emit(this.orgId);
      // },5000);


    });

    this.unSub_refreshList = _store.select('refreshList').subscribe(value => {
      console.log(value);
      if(value==null){
        return;
      }

      if(value == 'Reload Vehicle List') {
        this.vehicleMgmntService.getAllVehiclesByOrg.emit(this.orgId);
      }
    });


    this.unSub_vehiclesByOrgList = _store.select('getVehiclesByOrg').subscribe(value => {

      if (value == null) {
        return;
      }

      if(value['status'] == 200){
        console.log('Vehicles by group list');
      console.log(value);

      this.vehiclesByOrgList=value['list'];

      let sharedWithOrgList=[];

      for(let i=0; i<this.vehiclesByOrgList.length; i++){
        sharedWithOrgList= this.vehiclesByOrgList[i].sharedWith;
        this.vehiclesByOrgList[i]._sharedWithOrgList=sharedWithOrgList;
        sharedWithOrgList=sharedWithOrgList.splice(sharedWithOrgList.indexOf("FRETRON_GOD_FO"),1);

      }

      }

    });

    this.unSub_sharedOrgDetails = _store.select('getSharedOrgDetails').subscribe(value => {

      if (value == null) {
        return;
      }

      console.log(value);

        console.log('Shared Org Details');
        this.sharedOrganisationDetails = value;


    });

    this.unSub_addNewVehicle = _store.select('addVehicleResponse').subscribe(value => {

      if (value == null) {
        return;
      }

      if(value['status'] == 200)
      {
        this.isShowAddNewVehicle=false;
        this.openSnackBar("Vehicle Added Successfully.");
        setTimeout(() =>{
          this.vehicleMgmntService.getAllVehiclesByOrg.emit(this.orgId);
        },1500);

      }
      else {
        this.openSnackBar("Failed to Add Vehicle");
      }

    });


    this.unSub_shareVehicleResp = _store.select('shareVehicleResponse').subscribe(value => {

      if (value == null) {
        return;
      }

      if(value['status'] == 200)
      {
        this.isShowAddNewVehicle=false;
        this.openSnackBar("Vehicle(s) shared Successfully.");
        setTimeout(() =>{
          this.vehicleMgmntService.getAllVehiclesByOrg.emit(this.orgId);
        },1500);
      }
      else {
        this.openSnackBar("Failed to Share Vehicle. " + value['error']);
      }

    });

    this.unSub_sharedWithOrgList = _store.select('sharedWithOrgList').subscribe(value => {

      if (value == null) {
        return;
      }

      console.log(value);

      if(value['status']== 200)
      {
        console.log('Vehicle Shared with org list');
        this.vehicleSharedWithOrgList = value['list'];
      }
    });


    this.unSub_deleteVehicleFromOrgResponse = _store.select('deleteVehiclesFromOrg').subscribe(value => {

      if (value == null) {
        return;
      }

      console.log(value);

      if(value['status']== 200)
      {
        this.openSnackBar("Vehicle Deleted Successfully");
        this.isShowConfirmDelete=false;

          this.vehicleMgmntService.getAllVehiclesByOrg.emit(this.orgId);
      }
      else{
        this.openSnackBar("Vehicle Delete Failed");
      }


    });



    this.unSub_cancelSharing = _store.select('cancelSharingResponse').subscribe(value => {

      if (value == null) {
        return;
      }

      console.log(value);

      if(value['status']== 200)
      {
        console.log('Cancel sharing response');
        this.openSnackBar("Successfully cancelled sharing");
        setTimeout(() =>{
          this.vehicleMgmntService.getAllVehiclesByOrg.emit(this.orgId);
        },500);


      }

      else{
        this.openSnackBar("Failed to Cancel Sharing. " + value['error']);
        setTimeout(() =>{
          this.vehicleMgmntService.getAllVehiclesByOrg.emit(this.orgId);
        },500);
      }

    });

    this.vehicleMgmntService.getAllVehiclesByOrg.emit(this.orgId);
    this.unSub_allDeviceList = _store.select('allDriverList').subscribe(value => {
      console.log(value);
      if (value == null) {
        return;
      }
      if(value['error'] =='')
      {
        this.allDriverList=value['data'];
      }
      else {
        this.allDriverList=null;
      }
    });


  }

  ngOnInit() {
  }


  ngOnDestroy() {
    if (typeof this.unSub_vehiclesByOrgList != 'undefined' && this.unSub_vehiclesByOrgList != null && this.unSub_vehiclesByOrgList != undefined)
      this.unSub_vehiclesByOrgList.unsubscribe();

    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined)
      this.unSub_customerDetails.unsubscribe();

    if (typeof this.unSub_refreshList != 'undefined' && this.unSub_refreshList != null && this.unSub_refreshList != undefined)
      this.unSub_refreshList.unsubscribe();

    if (typeof this.unSub_addNewVehicle != 'undefined' && this.unSub_addNewVehicle != null && this.unSub_addNewVehicle != undefined)
      this.unSub_addNewVehicle.unsubscribe();

    if (typeof this.unSub_shareVehicleResp != 'undefined' && this.unSub_shareVehicleResp != null && this.unSub_shareVehicleResp != undefined)
      this.unSub_shareVehicleResp.unsubscribe();

    if (typeof this.unSub_sharedOrgDetails != 'undefined' && this.unSub_sharedOrgDetails != null && this.unSub_sharedOrgDetails != undefined)
      this.unSub_sharedOrgDetails.unsubscribe();

    if (typeof this.unSub_sharedWithOrgList != 'undefined' && this.unSub_sharedWithOrgList != null && this.unSub_sharedWithOrgList != undefined)
      this.unSub_sharedWithOrgList.unsubscribe();

    if (typeof this.unSub_cancelSharing != 'undefined' && this.unSub_cancelSharing != null && this.unSub_cancelSharing != undefined)
      this.unSub_cancelSharing.unsubscribe();

    if (typeof this.unSub_allDeviceList != 'undefined' && this.unSub_allDeviceList != null && this.unSub_allDeviceList != undefined)
      this.unSub_allDeviceList.unsubscribe();
    this._store.dispatch({type: "SHARE_VEHICLE_RESPONSE", payload: null});
    this._store.dispatch({type: "ADD_VEHICLE_RESPONSE", payload: null});
    this._store.dispatch({type: "CANCEL_SHARING_RESPONSE", payload: null});
    this._store.dispatch({type: "DELETE_VEHICLES_FROM_ORG_RESPONSE", payload: null});

  }

  showAddNewVehicle()
  {
    this.isShowAddNewVehicle=true;
    this.driverService.getAllDriverList.emit();
  }

  goToEditVehicles(vehicleObj)
  {
    this.localStorageService.set('LAST_CALLED_FROM_ORG_MANAGEMENT','vehicle List');
    this._store.dispatch({type: "CALLED_EDIT_VEHICLE_SOURCE",payload: "VehicleList"});
    this._store.dispatch({type: "SELECTED_VEHICLE_FOR_EDIT",payload: vehicleObj});
    this._store.dispatch(go(['/vtsdashboard/editVehicles']));
  }

  showShareModal()
  {
    console.log("share modal is here : ");
  }


  saveNewVehicles()
  {
    // let obj={
    //   vehcileNumber: this.vehicleNumber,
    //   vehcileType: this.vehicleType,
    //   vehicleStatus: this.vehicleStatus,
    //   vtsDeviceId: this.vtsDeviceIdVehicle,
    //   gpsVendor: this.gpsVendor
    // }

    let obje={
      vehicleType :  this.vehicleType,
      vehicleModel :  this.vehicleType,
      vehicleMake : this.vehicleType,
      vehicleRegistrationNumber : this.vehicleNumber,
      vtsDeviceId :this.vtsDeviceIdVehicle,
      driverId:this.driverId
    }

    // console.log(obj);
    console.log(obje);

    this.vehicleMgmntService.addNewVehicles.emit(obje);
    this._store.dispatch({type: "REFRESH_LIST",payload:'Reload Vehicle List'});
    this.isShowAddNewVehicle=false;


  }

  selectVehicleforShare(event,vehicle)
  {
    this.isSelcetedForShare=true;
    console.log(event);

    if(event.checked) {
      this.selectedVehicles.push(vehicle['uuid']);
    }
    if(!event.checked)
    {
      this.selectedVehicles.splice(this.selectedVehicles.indexOf(vehicle['uuid']),1);
    }

    if(!event.checked && this.selectedVehicles.length == 0)
    {
      this.isSelcetedForShare=false;
    }
    console.log(this.selectedVehicles);
  }

  showConfirmOrgDialogue()
  {
    this.isShowShareVehicle=false;
    this.vehicleMgmntService.getSharedOrg.emit(this.shareOrgId);
    this.isShowOrgConfirm=true;
  }

  shareVehicle()
  {
    this.isShowOrgConfirm=false;
    this.isShowShareVehicle=false;

   let shareVehObj={
     "orgId":this.sharedOrganisationDetails["organisation"]["uuid"],
     "vehicleList": this.selectedVehicles
    }

    this.vehicleMgmntService.shareSelecetedVehicles.emit(shareVehObj);
  }

  openSnackBar(message: string) {
    this.snackBar.open(message, 'OK', <MdSnackBarConfig>{ duration: 4000 });
  }




  showSharedVehicleData(vehicleObj)
  {
    console.log(vehicleObj);

    this._store.dispatch({type: "CANCEL_SHARING_RESPONSE", payload: null});

    let obj={
      "orgIdList" : vehicleObj['_sharedWithOrgList']
    }
    this.selectedVehicleForCancel=vehicleObj;
    this.vehicleMgmntService.getSharedWithList.emit(obj);
    this.isShowSharedDetails=true;

  }

  cancelSharing(sharedWithOrg)
  {
    console.log(sharedWithOrg);
    let obj={
      "vehicleId": this.selectedVehicleForCancel.uuid,
      "orgId":sharedWithOrg['uuid']
    }

    console.log(obj);

    this.vehicleMgmntService.cancelVehicleSharing.emit(obj);
    this.isShowSharedDetails=false;
  }
  setDriver(driver){
    console.log(driver);
    this.driverId=driver['uuid'];
    this.driverName=driver['name'];
    this.mobNo=driver['mobileNumber'];
    this.isShowDriver=false;
    this.isRemoveIcon=true;
  }
  removeDriver(){
    this.driverId=null;
    this.driverName=null;
    this.mobNo=null;
    this.isRemoveIcon=false;
  }
  addNewDriver(){
    this.isShowAddDriver=true;
  }
  showAddNewDriver(){
    this.isShowAddDriver=false;
  }
  getDriverDitail(driver){
    console.log(driver);
    if(driver != null || driver != undefined){
    this.driverId=driver['uuid'];
    this.driverName=driver['name'];
    this.mobNo=driver['mobileNumber'];
    this.isRemoveIcon=true;
    this.isShowDriver=false;
    }

  }

  showConfirmDeleteDialogue(vehicleObj)
  {
    console.log("Selected Vehcicle for Delete:");
    console.log(vehicleObj);

    this.isShowConfirmDelete=true;
    this.selectedVehicleForDelete= vehicleObj;
  }


  deleteVehicle()
  {
    this.isShowConfirmDelete=false;
    this.vehicleMgmntService.deleteSelecetedVehicleFromOrg.emit(this.selectedVehicleForDelete);
  }

}



