import { Injectable,EventEmitter } from '@angular/core';
import { Http, Headers, URLSearchParams }                    from '@angular/http';
import { RequestMethod, RequestOptions, RequestOptionsArgs } from '@angular/http';
import { Response, ResponseContentType }                     from '@angular/http';

import { Observable }                                        from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {Store} from "@ngrx/store";
import {LocalStorageService} from "angular-2-local-storage";
import {paths} from "../../environments/environment";

@Injectable()
export class UnifiedDashboardService {
  public defaultHeaders: Headers = new Headers();
  public getTripStatus=new EventEmitter();
  public getTripDetails=new EventEmitter();

  constructor(protected http: Http,private _store:Store<any>,private localStorageService: LocalStorageService) {
    //************ getTripStatus all list of trip  ***************************************************
    this.getTripStatus.subscribe(()=> {
      // console.log(TRIP_STATUS_PATH);
      console.log("in trip status");
      this.getAllTripStatus().subscribe(response =>{
        // console.log(response);
        this._store.dispatch({type: "UNIFIED_TRIP_STATUS_LIST",payload:response});
      },err=>{
        // this._store.dispatch({type: "AUTHORIZATION",payload:{valid:false,error:'X',msg:err.json()}});
        // this.localStorageService.set("TOKEN",false);
        console.log(err);
        // this._store.dispatch({type: "TRIP_STATUS_LIST",payload:err.json()});
      });
    });

    //@todo handle error cases
    this.getTripDetails.subscribe((obj)=> {
      let path=paths.TRIP_STATUS_DETAILS_PATH+""+obj['vehicleID'];
      // console.log(path);
      this.getVehicleTripDetails(path).subscribe(response =>{
        // console.log(response);
        this._store.dispatch({type: "UNIFIED_VEHICLE_TRIP_DETAILS",payload:response});
      },err=>{
        console.log(err);
      });
    });
  } // constuctor closes here

  // *********************** getAllTripStatus  **********************
  public getAllTripStatus(){
    let path = paths.TRIP_PATH;
    // let queryParameters = new URLSearchParams();
    let headers = new Headers(this.defaultHeaders.toJSON());
    headers.set('Content-Type', 'application/x-www-form-urlencoded; charset=UTF-8');
    let token=this.localStorageService.get('AUTH_TOKEN');
    let bearerToken="Bearer "+token;
    headers.set('authorization', bearerToken);

    // console.log(headers);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
      // body: vList,
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      // console.log(response);
      // if(response.status==200){
      return response.json();
      // }else{
      //   console.log("not 200");
      //   return {error:'X'};
      // }
    });
  }


  // *********************** getVehicleTripDetails of selected vehicle **********************
  public getVehicleTripDetails(path){

    let headers = new Headers(this.defaultHeaders.toJSON());
    headers.set('Content-Type', 'application/json');
    let token=this.localStorageService.get('TOKEN');
    let bearerToken="Bearer "+token;
    headers.set('authorization', bearerToken); // console.log(headers);
    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
      // body: vList,
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      // console.log(response);
      // if(response.status==200){
      return response.json();
      // }else{
      //   console.log("not 200");
      //   return {error:'X'};
      // }
    });
  }

}



