import { Component, OnInit } from '@angular/core';
import {AlertService} from "app/services/alert-services";
import {Store} from "@ngrx/store";

@Component({
  selector: 'app-alerts-notification',
  templateUrl: './alerts-notification.component.html',
  styleUrls: ['./alerts-notification.component.css']
})
export class AlertsNotificationComponent implements OnInit {

  public unsub_getAlertsNotification:any=null;
  public unsub_getNotificationForSelecetedVeh:any=null;
  public allNotificationList:any;
  public selectedVehicle:any;

  constructor(private _store:Store<any>,public alertServices:AlertService) {

    this._store.dispatch({type: "TITLE",payload:'Alerts Notifications'});

    this.unsub_getAlertsNotification=this._store.select('getAllAlertsNotification').subscribe(value => {
      console.log("Alerts in Notification :");
      console.log(value);



      if(value==null){
        return;
      }
      let notificationLists:any[];

      this.allNotificationList=value['data'];
      this.allNotificationList=this.allNotificationList.reverse();
      // notificationLists=value;
      // if(notificationLists.length == 0)
      // {
      //
      // }

      this._store.select('selectedVehicleforAlert').subscribe(value => {
        console.log("Vehicle in Alerts Notification :");
        console.log(value);

        if(value==null){
          return;
        }

        if(value != null || value!= undefined) {
          this.selectedVehicle=value;
          console.log("Selected vehicle for filter");
          let obj={};
          for(let i=0; i<this.allNotificationList.length; i++){
            if(this.selectedVehicle['uuid'] == this.allNotificationList[i]['vehicleId'])
            {
              obj=this.allNotificationList[i];
              break;
            }
          }

          this.allNotificationList=[];
          this.allNotificationList.push(obj);
        }
      });

    });
    this.alertServices.getAlertsNotification.emit();
  }

  ngOnInit() {



  }

  ngOnDestroy()
  {
    if (typeof this.unsub_getAlertsNotification != 'undefined' && this.unsub_getAlertsNotification != null && this.unsub_getAlertsNotification != undefined)
      this.unsub_getAlertsNotification.unsubscribe();
    if (typeof this.unsub_getNotificationForSelecetedVeh != 'undefined' && this.unsub_getNotificationForSelecetedVeh != null && this.unsub_getNotificationForSelecetedVeh != undefined)
      this.unsub_getNotificationForSelecetedVeh.unsubscribe();

    this._store.dispatch({type: "SELECTED_VEHICLE_FOR_ALERT",payload: null});
  }

}



