import { Component, OnInit } from '@angular/core';
import {Store} from "@ngrx/store";
import {go} from "@ngrx/router-store";
import {LocalStorageService} from "angular-2-local-storage";
import { DriverManagementService } from "../../services/driver-registry-service";
import { MdSnackBar, MdSnackBarConfig } from "@angular/material";
import { isNullOrUndefined } from "util";

@Component({
  selector: 'app-manage-driver',
  templateUrl: './manage-driver.component.html',
  styleUrls: ['./manage-driver.component.css']
})
export class ManageDriverComponent implements OnInit {
  public view='';
  public allDriverList:any[]=[];
  public unSub_allDeviceList=null;
  public unSub_driverCommand=null;
  public isShowAddDriver=false;
  public isShowEditDriver=false;
  public isShowDeleteDriver=false;  
  public driverName:any;
  public mobileNumber:any;
  public dlNumber:any;
  public dlExpiryTime:any;
  public driver:any;
  constructor(private _store:Store<any>,private driverService:DriverManagementService,public snackBar: MdSnackBar,private localStorageService: LocalStorageService) {
    this.view =document['view'];
    this._store.dispatch({type: "TITLE",payload:'Driver Management'});// set the header name
    // this.allDriverList=this.driverList;
    this.driverService.getAllDriverList.emit();
    this.unSub_allDeviceList = _store.select('allDriverList').subscribe(value => {
      console.log(value);
      if (value == null) {
        return;
      }
      if(value['error'] =='')
      {
        this.allDriverList=value['data'];
         for (let i = 0; i <this.allDriverList.length; i++) {
           if(this.allDriverList[i]['dlExpiryTime']!=null){
            this.allDriverList[i]['_dlDate']=this.changeTimeToDate(this.allDriverList[i]['dlExpiryTime']);
           }
           else{
            this.allDriverList[i]['_dlDate']=null;
           }
         }
      }
      else {
        this.allDriverList=null;
      }
    });

    this.unSub_driverCommand = _store.select('driverCommand').subscribe(value => {
      if (value == null) {
        return;
      }

      if(value['error'] =='')
      {
        this.openSnackBar(value['msg']);        
      }
      else {
        this.openSnackBar(value['msg']); 
      }

    });
 
  }

  ngOnInit() {
  }
  ngOnDestroy() {
    if (typeof this.unSub_allDeviceList != 'undefined' && this.unSub_allDeviceList != null && this.unSub_allDeviceList != undefined)
      this.unSub_allDeviceList.unsubscribe();
    if (typeof this.unSub_driverCommand != 'undefined' && this.unSub_driverCommand != null && this.unSub_driverCommand != undefined)
      this.unSub_driverCommand.unsubscribe();
    this._store.dispatch({type: "DRIVER_COMMAND" ,payload:null});      
  }

  getDriverDitail(value){
    let newDriver=value;
    console.log(newDriver);
  }

  showAddNewDriver(value){
    this.isShowAddDriver=false;
  }
  goToEditDriver(driver){
    this.driver=driver;
    this.driverName=driver['name'];
    this.mobileNumber=driver['mobileNumber'];
    this.dlNumber=this.driver['dlNumber'];
    this.isShowEditDriver=true;
    console.log(driver);
  }
  showDeleteDialog(value){
    this.driver=value;
    this.driverName=value['name'];    
    this.isShowDeleteDriver=true;
  }
  saveDeleteDriver(){
   this.driverService.deleteDriver.emit(this.driver['uuid']);
   this.isShowDeleteDriver=false;   
  }

  saveEditDriver(){
    let time;
    if (isNullOrUndefined(this.mobileNumber) || this.mobileNumber == "") {
      this.openSnackBar("Please fill Trip Origin");
      return;
    }
    if (isNullOrUndefined(this.dlNumber) || this.dlNumber == "") {
      this.dlNumber=this.driver['dlNumber'];
    }
    if (isNullOrUndefined(this.dlExpiryTime) || this.dlExpiryTime == "") {
      time=this.driver['dlExpiryTime'];
    }
    else{
      time=new Date(this.dlExpiryTime).getTime();
      console.log(time);
    }
    let obj={
      'uuid':this.driver['uuid'],
      'mobileNumber':this.mobileNumber,
      'dlNumber':this.dlNumber,
      'dlExpiryTime':time
    }
    console.log(obj);
    this.driverService.updateDriver.emit(obj);
    this.isShowEditDriver=false;    
  }
  openSnackBar(message: string) {
    this.snackBar.open(message, 'OK', <MdSnackBarConfig>{ duration: 4000 });
  }
  goToManageOrganisationView(){
    this._store.dispatch(go(['/vtsdashboard/manageOrganisation']));
  }

  changeTimeToDate(value){
     let date=new Date(value);
    //  console.log(date.getFullYear());
    return ''+date.getDate()+'-'+this.getMonthName(date.getMonth())+'-'+date.getFullYear();
  }
  getMonthName(month)
  {
    return ["Jan","Feb","Mar","Apr","May","Jun","Jul","Aug","Sep", "Oct","Nov","Dec"][parseInt(month)]
  }
}



