import { Component, OnInit,Input,Output,EventEmitter } from '@angular/core';
import { DriverManagementService } from "../../../services/driver-registry-service";
import { MdSnackBar, MdSnackBarConfig } from "@angular/material";
import {Store} from "@ngrx/store";
import { isNullOrUndefined } from "util";


@Component({
  selector: 'app-add-driver-component',
  templateUrl: './add-driver-component.component.html',
  styleUrls: ['./add-driver-component.component.css']
})
export class AddDriverComponentComponent implements OnInit {
  @Output() driverDitail = new EventEmitter<any>();
  @Output() isAddDriverDialog = new EventEmitter<any>(); 
  public unSub_driverCommand=null;
  public driverObj:any={}; 
  public driverName:any;
  public mobileNumber:any;
  public dlNumber:any;
  public dlExpiryTime:any;
  public view='';
  
  constructor(private _store:Store<any>,private driverService:DriverManagementService,public snackBar: MdSnackBar) {
    this.view =document['view'];
    this.unSub_driverCommand = _store.select('addDriver').subscribe(value => {
     console.log(value);
      if (value == null) {
        return;
      }

      if(value['error'] =='')
      {
        this.openSnackBar("Driver added succssfully"); 
        this.saveDriver(value['driver']);
      }
      else {
        this.openSnackBar(value['response']['error']); 
        this.saveDriver(value['driver']);        
      }

    });
   }

  ngOnInit() {
  }
 saveNewDriver(){
   let time;
  if (isNullOrUndefined(this.driverName) || this.driverName == "") {
    this.openSnackBar("Please fill Driver Name");
    return;
  }
  if (isNullOrUndefined(this.mobileNumber) || this.mobileNumber == "") {
    this.openSnackBar("Please fill Mobile Number");
    return;
  }
  if (isNullOrUndefined(this.dlExpiryTime) || this.dlExpiryTime == "") {
    time=null;
  }
  else{
    time=new Date(this.dlExpiryTime).getTime();
    console.log(time);
  }
  
   let obj={
     'name':this.driverName,
     'mobileNumber':this.mobileNumber,
     'dlNumber':this.dlNumber,
     'dlExpiryTime':time
   };
   this.driverObj=obj;   
   console.log(obj);
   this.driverService.createDriver.emit(obj);
 }
  openSnackBar(message: string) {
    this.snackBar.open(message, 'OK', <MdSnackBarConfig>{ duration: 4000 });
  }
  saveDriver(value){
    this.driverDitail.emit(value)
    this.closeDialog(); 
  }
  closeDialog(){
   this.isAddDriverDialog.emit();
  }
  ngOnDestroy() {
    this._store.dispatch({type: "ADD_DRIVER" ,payload:null});          
  }

}



