  public distanceToGo: any;
  public eta: any;
  public timeValue: any;
  public templateObj: any = null;

  public vehicleTripDetails: Object = {};
  public vehicleTripDetailsClone: Object = {};
  public bestAddress = "";
  public isDelayed = false;
  public delayedBy: any;
  public movementLogs: any = {};
  public vehicleState: any;
  public latLngObj = null;
  public stoppedPolyLineArray:any= [];

  public allDrawElementOnMap:any= [];


  ngOnDestroy() {
    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined) {
      this.unSub_customerDetails.unsubscribe();
    }

    if (this.unSub_vehicleTripDetails != null && this.unSub_vehicleTripDetails != undefined)
      this.unSub_vehicleTripDetails.unsubscribe();

    if ( this.unSub_templateDetails != null && this.unSub_templateDetails != undefined)
      this.unSub_templateDetails.unsubscribe();
  }

