import { Component, OnInit } from '@angular/core';
import {Store} from "@ngrx/store";
import {go} from "@ngrx/router-store";
import {LocalStorageService} from "angular-2-local-storage";

@Component({
  selector: 'app-manage-organisation',
  templateUrl: './manage-organisation.component.html',
  styleUrls: ['./manage-organisation.component.css']
})
export class ManageOrganisationComponent implements OnInit {

  public view ='';
  public token:any;
  private unSub_customerDetails: any =null;
  public allUsersList:any=[];
  public orgId:string ='';
  public isGodShow : boolean = false;

  ngOnDestroy() {
    
    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined){
      console.log("unSub tpi VehicleList");
      this.unSub_customerDetails.unsubscribe();
    }
  }

  constructor(private _store:Store<any>, public _localStorageServices: LocalStorageService) {
    this.view=document['view'];
    this._store.dispatch({type: "TITLE",payload:'Manage Organisation'});// set the header name

    let _authToken=this._localStorageServices.get("AUTH_TOKEN");
    this.token=this._localStorageServices.get("AUTH_TOKEN");

    this.unSub_customerDetails = _store.select('customerDetails').subscribe(value => {
      console.log(value);
      this.allUsersList=[];
      if(value==null){
        return;
      }

      this.orgId=value['customer']['orgId'];
      if(value['customer']['isGod'] == true){
        this.isGodShow=true;
      }
     
    });
  }

  ngOnInit() {
  }

  goToUserManagement(){
    this._store.dispatch(go(['/vtsdashboard/userManagementConsole']));
  }

  goToManageVehicles(){
    this._store.dispatch(go(['/vtsdashboard/manageVehicles']));
  }

  goToTripTemplate()
  {
    this._store.dispatch(go(['/vtsdashboard/tripTemplateStatus']));
  }
  goToDriverRegistry()
  {
    this._store.dispatch(go(['/vtsdashboard/manageDriver']));
  }

  goToThirdPartyIntegration()
  {
    this._store.dispatch(go(['/vtsdashboard/thirdPartyIntegration']));
  }

}



