import { Component, OnInit } from '@angular/core';
import {Store} from "@ngrx/store";
import {go} from "@ngrx/router-store";
import {VehicleManagementService} from "../../../services/vehicle-management-services";
import {LocalStorageService} from "angular-2-local-storage";
import { MdSnackBar, MdSnackBarConfig } from "@angular/material";

@Component({
  selector: 'app-manage-vehicle-group',
  templateUrl: './manage-vehicle-group.component.html',
  styleUrls: ['./manage-vehicle-group.component.css']
})
export class ManageVehicleGroupComponent implements OnInit {

  public isShowAddNewGroup:boolean=false;
  public iscreateErrMsg:boolean=false;
  public isSelectForShare:boolean=false;
  public isShowShareGroup:boolean=false;
  public isShowConfirmOrg:boolean=false;
  public isShowSharedWithOrgList:boolean=false;


  public view ='';
  public groupName:string='';
  public orgId:string='';
  public shareOrgId:string='';
  public groupId:string='';
  public vehicleNumber:string='';
  public editVehicleList:any=[];
  public vehicleGroupList:any=[];




  public vehicleList:any;
  public sharedOrgDetails:any;
  public groupSelectedforCancel:any;
  public groupSharedWithOrgList:any=[];
  public selectedGroupToShare:any;
  public vehicles:any[]=[];
  public vehiclesToAddInGroup:any[]=[];

  private unSub_VehicleGroupList: any =null;
  private unSub_PickedVehicles: any =null;
  private unSub_createGroupResponse: any =null;
  private unSub_addVehicleInGroupResponse: any =null;
  private unSub_customerDetails: any =null;
  private unSub_sharedOrgDetails: any =null;
  private unSub_shareVehicleGroupResponse: any =null;
  private unSub_sharedWithOrgList: any =null;
  private unSub_cancelGroupShareResponse: any =null;

  constructor(private _store:Store<any>,public vehicleGrpMgmntService:VehicleManagementService,private localStorageService: LocalStorageService,public snackBar: MdSnackBar)
  {
    this.view=document['view'];
    this.unSub_customerDetails = _store.select('customerDetails').subscribe(value => {
      console.log(value);
      if(value==null){
        return;
      }

      this.orgId=value['customer']['orgId'];
      this.vehicleGrpMgmntService.getAllVehicleGroupsList.emit(this.orgId);
    });

    this.unSub_PickedVehicles = _store.select('PickedVehicles').subscribe(value => {

      console.log(value);
      this.vehicleList = value;

      this.vehicles=[];
      for(let i=0;i<this.vehicleList.length;i++){
        this.vehicles.push(this.vehicleList[i]['uuid']);
        this.vehiclesToAddInGroup.push(this.vehicleList[i]['uuid']);
      }

      console.log(this.vehicles);
    });

    this.unSub_VehicleGroupList = _store.select('getAllVehiclesGroupList').subscribe(value => {

      if(value==null)
      {
        return;
      }
      console.log('all vehicle group list in component');
      console.log(value);
      if(value['status']==200)
      {
        this.vehicleGroupList=value['list'];
      }
    });

    this.unSub_createGroupResponse = _store.select('createGroupResponse').subscribe(value => {

      if(value==null)
      {
        return;
      }
      console.log('create group response :');
      console.log(value);
      this.groupId=value['organisationGroup']['uuid'];
      console.log(this.groupId);

      this.addVehiclesToGroup(this.groupId);

    });

    this.unSub_cancelGroupShareResponse = _store.select('cancelSharingGroupResponse').subscribe(value => {

      if(value==null)
      {
        return;
      }
      if(value['status']==200)
      {
        this.openSnackBar("Successfully Cancelled vehicle Group share");
        this.vehicleGrpMgmntService.getAllVehicleGroupsList.emit(this.orgId);
      }

      else
      {
        this.openSnackBar("Failed to cancel share "+value['error']);
      }

      this._store.dispatch({type: "CANCEL_SHARING_GROUP_RESPONSE", payload: null});
    });


    this.unSub_addVehicleInGroupResponse = _store.select('addVehiclesInGroup').subscribe(value => {

      if(value==null)
      {
        return;
      }
      console.log('add vehicles in group response :');
      console.log(value);

      this.vehicleGrpMgmntService.getAllVehicleGroupsList.emit(this.orgId);
    });

    this.unSub_sharedOrgDetails = _store.select('getSharedOrgDetails').subscribe(value => {

      if (value == null) {
        return;
      }

      console.log(value);

        console.log('Shared Org Details');
        this.sharedOrgDetails = value;


    });


    this.unSub_shareVehicleGroupResponse = _store.select('shareVehGroupResponse').subscribe(value => {

      if(value==null)
      {
        return;
      }
      console.log('share group response :');
      console.log(value);

      if(value['status']==200)
      {
        this.openSnackBar("Vehicle Group Shared Successufully");
      }

      if(value['status']==400)
      {
        this.openSnackBar(value['error']);
      }


      this.vehicleGrpMgmntService.getAllVehicleGroupsList.emit(this.orgId);
    });


    this.unSub_sharedWithOrgList = _store.select('sharedWithOrgList').subscribe(value => {

      if (value == null) {
        return;
      }

      console.log(value);

      if(value['status']== 200)
      {
        console.log('Vehicle Shared with org list');
        this.groupSharedWithOrgList = value['list'];
      }
    });

  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (typeof this.unSub_VehicleGroupList != 'undefined' && this.unSub_VehicleGroupList != null && this.unSub_VehicleGroupList != undefined)
      this.unSub_VehicleGroupList.unsubscribe();

    if (typeof this.unSub_PickedVehicles != 'undefined' && this.unSub_PickedVehicles != null && this.unSub_PickedVehicles != undefined)
      this.unSub_PickedVehicles.unsubscribe();

    if (typeof this.unSub_createGroupResponse != 'undefined' && this.unSub_createGroupResponse != null && this.unSub_createGroupResponse != undefined)
      this.unSub_createGroupResponse.unsubscribe();

    if (typeof this.unSub_addVehicleInGroupResponse != 'undefined' && this.unSub_createGroupResponse != null && this.unSub_createGroupResponse != undefined)
      this.unSub_addVehicleInGroupResponse.unsubscribe();

    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined)
      this.unSub_customerDetails.unsubscribe();

    if (typeof this.unSub_sharedOrgDetails != 'undefined' && this.unSub_sharedOrgDetails != null && this.unSub_sharedOrgDetails != undefined)
      this.unSub_sharedOrgDetails.unsubscribe();

    if (typeof this.unSub_shareVehicleGroupResponse != 'undefined' && this.unSub_shareVehicleGroupResponse != null && this.unSub_shareVehicleGroupResponse != undefined)
      this.unSub_shareVehicleGroupResponse.unsubscribe();

    this._store.dispatch({type: "SHARE_VEHICLE_GROUP_RESPONSE", payload: null});
    this._store.dispatch({type: "CANCEL_SHARING_GROUP_RESPONSE", payload: null});

  }

  goToGroupedVehiclesList(groupObj)
  {
    this._store.dispatch({type: "SELECTED_VEH_GROUP_FOR_EDIT",payload: groupObj});
    this._store.dispatch(go(['/vtsdashboard/groupedVehiclesList']));
  }

  showAddNewGroup()
  {
    this.isShowAddNewGroup=true;
  }

  createNewGroup()
  {
    if(this.groupName != "" && this.vehicles.length > 0) // create group only if name and atleast one vehicle is selected
    {
      this.iscreateErrMsg=false;
      this.isShowAddNewGroup=false;
      let grpObj={
        orgId : this.orgId,
        groupName : this.groupName,
        groupType: "vehicleGroup"
      }
      this.vehicleGrpMgmntService.createNewGroup.emit(grpObj);
    }
    this.iscreateErrMsg=true;
  }

  addVehiclesToGroup(_groupId)
  {
    console.log("add event emmitter to add vehicles to group");
    let  obj={
      groupId : _groupId,
      vehicleList : this.vehiclesToAddInGroup
    }

    console.log(obj);

    this.vehicleGrpMgmntService.addVehiclesToGroup.emit(obj);
  }

  selectGroupForShare(groupObj,event)
  {
    if(event.checked) {
      this.isSelectForShare = true;
    }
    else{
      this.isSelectForShare= false;
    }
    console.log(groupObj);
    for(let i=0; i<this.vehicleGroupList.length; i++){

      this.vehicleGroupList[i]._isChecked=false;
      if(this.vehicleGroupList[i]== groupObj)
      {
        this.vehicleGroupList[i]._isChecked=true;
      }
    }
    this.selectedGroupToShare=groupObj;

    console.log(this.vehicleGroupList);
  }

  showConfirmOrgDialogue()
  {
    this.isShowShareGroup=false;
    this.vehicleGrpMgmntService.getSharedOrg.emit(this.shareOrgId);
    this.isShowConfirmOrg=true;
  }

  shareGroup()
  {
    console.log(this.selectedGroupToShare);
    // this.vehicleGrpMgmntService.

    this.isShowConfirmOrg=false;

    let shareObj={
      "orgId": this.sharedOrgDetails['organisation']['uuid'],
      "groupId": this.selectedGroupToShare['uuid']
    };


    this.vehicleGrpMgmntService.shareSelecetedGroup.emit(shareObj);
  }

  showSharedGroupData(groupDataObj)
  {
    console.log(groupDataObj);    
    let obj={
      "orgIdList" : groupDataObj['sharedWith']
    }
    this.vehicleGrpMgmntService.getSharedWithList.emit(obj);
    this.isShowSharedWithOrgList=true;
    this.groupSelectedforCancel=groupDataObj;
  }

  cancelGroupSharing(sharedWithOrg)
  {
    console.log(sharedWithOrg);

    this.isShowSharedWithOrgList=false;

    let obj={
      "groupId": this.groupSelectedforCancel.uuid,
      "orgId":sharedWithOrg['uuid']
    }
    console.log(obj);
    this.vehicleGrpMgmntService.cancelVehicleGroupSharing.emit(obj);
  }

  openSnackBar(message: string) {
    this.snackBar.open(message, 'OK', <MdSnackBarConfig>{ duration: 4000 });
  }
}



