import { Component, OnInit } from '@angular/core';
import {Store} from "@ngrx/store";
import {go} from "@ngrx/router-store";

import { TataFleetmanService } from '../../../services/tata-fleetman-service';
import { paths } from 'environments/environment.prod';
import {MdSnackBar, MdSnackBarConfig} from "@angular/material";

@Component({
  selector: 'app-tpi-etrans-form',
  templateUrl: './tpi-etrans-form.component.html',
  styleUrls: ['./tpi-etrans-form.component.css']
})
export class TpiEtransFormComponent implements OnInit {

  public unSub_etransVehicleList = null;
  public view ='';
  public isSpinner= false;
  public loginObj ={
    "eid" : "",
    "key" : "",
   }

   ngOnDestroy() {
    
    if (typeof this.unSub_etransVehicleList != 'undefined' && this.unSub_etransVehicleList != null && this.unSub_etransVehicleList != undefined){
      console.log("unSub Etrans VehicleList");
      this.unSub_etransVehicleList.unsubscribe();
    }
  }

  constructor(private _store:Store<any>,private tataFleetmanService:TataFleetmanService, public snackBar: MdSnackBar) {

    this.view=document['view'];

    this.unSub_etransVehicleList = _store.select('etransVehicleList').subscribe(value => {
      //console.log(value);
      if(value==null)
      {
        return;
      }
      
      if(value['error'] == '') {
        this.isSpinner=false;
        console.log(value);     
        this._store.dispatch(go(['/vtsdashboard/tpiIdentifiedVehicles']));
      }
        else {
          this.isSpinner=false;
          this.openSnackBar(" UNAUTHORISED ACCESS ");
          console.log(value['errorMsg']);
        
      }
    });
  }

  ngOnInit() {
  }

  goToTpi(){
    this._store.dispatch(go(['/vtsdashboard/thirdPartyIntegration']));
  }

  goToSubmit(){
    if(this.loginObj.eid=='' || this.loginObj.eid == undefined){
      console.log('Please input Id');
      this.openSnackBar("Please Input ID");
      return false;
  }
    else if(this.loginObj.key ==''|| this.loginObj.key ==undefined){
      console.log('Key required');
      this.openSnackBar("Key Required");
      return false;
  }
    else {
      this.isSpinner= true;
      this.tataFleetmanService.getEtransVehicleList.emit(this.loginObj);
      }
  }
  openSnackBar(message: string) {
      this.snackBar.open(message , '', <MdSnackBarConfig>{duration :  3000});
     }


  }






