import { Component, OnInit } from '@angular/core';
import {Store} from "@ngrx/store";
import {go} from "@ngrx/router-store";

import { TataFleetmanService } from '../../../services/tata-fleetman-service';
import { paths } from 'environments/environment.prod';
import {MdSnackBar, MdSnackBarConfig} from "@angular/material";


@Component({
  selector: 'app-rooster-info',
  templateUrl: './rooster-info.component.html',
  styleUrls: ['./rooster-info.component.css']
})
export class RoosterInfoComponent implements OnInit {
  
  public unSub_generateUrl = null;
  public view ='';
  public urll : any;
  public obj ={
    "url" : "",
    "status" : "",
   }
  ngOnDestroy() {
    
    if (typeof this.unSub_generateUrl != 'undefined' && this.unSub_generateUrl != null && this.unSub_generateUrl != undefined){
      console.log("unSub Generate Url");
      this.unSub_generateUrl.unsubscribe();
    }
  }

  constructor(private _store:Store<any>, private tataFleetmanService:TataFleetmanService,public snackBar: MdSnackBar) { 

    this.view=document['view'];

    this.unSub_generateUrl = _store.select('generateUrl').subscribe(value => {
      //console.log(value);
      if(value==null)
      {
        return;
      }
      
      if(value['res']['status'] == 200 ) {
        this.obj = value['res'];
       // this.isSpinner=false;
        console.log(this.obj);     
      }
        else {
          console.log(value['errorMsg']);
        
      }
    });
   
  }

  ngOnInit() {
  }
  goToGenerateUrl(){
   
    this.tataFleetmanService.getUrl.emit(this.obj);
 
  }
  goToTpi(){
    this._store.dispatch(go(['/vtsdashboard/thirdPartyIntegration']));
  }

  copyToClipboard(){
    document.querySelector('input').select();
    document.execCommand('copy');
  }

}



