import { Component,Inject, Injectable, Optional,EventEmitter,Output }                      from '@angular/core';
import { Http, Headers, URLSearchParams }                    from '@angular/http';
import { RequestMethod, RequestOptions, RequestOptionsArgs } from '@angular/http';
import { Response, ResponseContentType }                     from '@angular/http';


import { Observable }                                        from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {Store} from "@ngrx/store";
import {LocalStorageService} from "angular-2-local-storage";
import {paths} from "../../environments/environment";

@Injectable()
export class DriverManagementService {

  public defaultHeaders: Headers = new Headers();
  public createDriver =new EventEmitter();
  public getAllDriverList = new EventEmitter();  
  public deleteDriver = new EventEmitter();
  public updateDriver=new EventEmitter();
  

  public token;

  constructor(protected http: Http, private _store: Store<any>, private localStorageService: LocalStorageService) {
    
        this.token=this.localStorageService.get("AUTH_TOKEN");
    
    
        //@todo handle error cases
        this.getAllDriverList.subscribe(() => {
          let path = paths.DRIVER_MANAGER+ "/getList";
          console.log(path);
          this.getAllDriverService(path).subscribe(response => {
            console.log(response);
            console.log(response['status']);
            
            if(response['status']==200)
            this._store.dispatch({type: "ALL_DRIVER_LIST",payload:{error:'',data:response['list']}});
            else
            this._store.dispatch({type: "ALL_DRIVER_LIST",payload:{error:'X',errorMsg:''}});
            
          },err=>{
            console.log(err);
            this._store.dispatch({type: "ALL_DRIVER_LIST",payload:{error:'X',errorMsg:''}});            
          });
        });

        this.createDriver.subscribe((driverData)=> {
            let path=paths.DRIVER_MANAGER+'/add';
            console.log(path);
            //@todo error handling
            this.createDriverService(path,driverData).subscribe(response =>{
              console.log(response);
            if(response['status']==200) {           
              this._store.dispatch({type: "ADD_DRIVER" ,payload:{error:'',response}});
              this.getAllDriverList.emit();
            }
            else  
            this._store.dispatch({type: "ADD_DRIVER" ,payload:{error:'X',response}});
            
            },err=>{
              console.log(err);
              this._store.dispatch({type: "ADD_DRIVER" ,payload:{error:'X',response:{error:'Server Error,Try again'}}});
              
            });
          });

          this.updateDriver.subscribe((driverData)=> {
            let path=paths.DRIVER_MANAGER+'/update';
            console.log(path);
            //@todo error handling
            this.createDriverService(path,driverData).subscribe(response =>{
              console.log(response);
              if(response['status']==200){ 
                this._store.dispatch({type: "DRIVER_COMMAND" ,payload:{error:'',msg:'Driver updated Successfully'}});
              this.getAllDriverList.emit();
              }
              else  
              this._store.dispatch({type: "DRIVER_COMMAND" ,payload:{error:'',msg:'Server Error,Try again'}});
              },err=>{
              this._store.dispatch({type: "DRIVER_COMMAND" ,payload:{error:'X',msg:'Server Error,Try again'}});
              
              console.log(err);
            });
          });

          this.deleteDriver.subscribe((driverData)=> {
            let path=paths.DRIVER_MANAGER+'/delete?uuid='+driverData;
            console.log(path);
            //@todo error handling
            this.createDriverService(path,driverData).subscribe(response =>{
              console.log(response);
              if(response['status']==200){ 
                this._store.dispatch({type: "DRIVER_COMMAND" ,payload:{error:'',msg:'Driver deleted Successfully'}});
              this.getAllDriverList.emit();
              }
              else  
              this._store.dispatch({type: "DRIVER_COMMAND" ,payload:{error:'',msg:'Server Error,Try again'}});
              },err=>{
                this._store.dispatch({type: "DRIVER_COMMAND" ,payload:{error:'X',msg:'Server Error,Try again'}});
                
                console.log(err);
              });
          });
    }//constructor

    /**************************get all driver *******************************/
    public getAllDriverService(path){
        let headers = new Headers(this.defaultHeaders.toJSON());
        let bearerToken="Bearer "+this.token;
        headers.set('Content-Type', 'application/json');
        headers.set('authorization', bearerToken);
    
        let requestOptions: RequestOptionsArgs = new RequestOptions({
          method: RequestMethod.Get,
          headers: headers,
        });
        return this.http.request(path, requestOptions).map((response: Response) => {
          // console.log(response.status);
          console.log(response);
          if(response.status ==200) {
            return response.json();
          }
    
        });
      }

      // *********************** create  Driver service  **********************
  public createDriverService(path,driverData)
  {
    // let queryParameters = new URLSearchParams();
    let headers = new Headers(this.defaultHeaders.toJSON());
    let bearerToken="Bearer "+this.token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);
    console.log(driverData);
    console.log(headers);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body: driverData,
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
     console.log(response);
      return response.json();

    });
  }

  // *********************** update Driver service  **********************
  public updateDriverService(path,updateDriver){

    let headers = new Headers(this.defaultHeaders.toJSON());
    let bearerToken="Bearer "+this.token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body: updateDriver,
    });
    return this.http.request(path, requestOptions).map((response: Response) => {

      return response.json();

    });
  }
  // *********************** delete Driver service **********************
  public deleteDriverService(path)
  {

    let headers = new Headers(this.defaultHeaders.toJSON());
    let bearerToken="Bearer "+this.token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
        method: RequestMethod.Get,
        headers: headers,
    });
    return this.http.request(path, requestOptions).map((response: Response) => {

        return response.json();

    });
  }
    
}


