import { Component, OnInit } from '@angular/core';
import {Store} from '@ngrx/store';
import {LocalStorageService} from 'angular-2-local-storage';
import {vehicleForLocationHistory} from '../../../reducers/info_store';

@Component({
  selector: 'app-desktop-view-dashboard',
  templateUrl: './desktop-view-dashboard.component.html',
  styleUrls: ['./desktop-view-dashboard.component.css']
})
export class DesktopViewDashboardComponent implements OnInit {

  public status='';
  public doughnutChartData: number[] = [55, 50, 90];
  public doughnutChartType: string = 'doughnut';
  public doughnutColor: any[] = [{backgroundColor: ["#add160", "#a7a9b2", "#FFAB00"]}];
  public doughnutChartLabels: any[] = ['Connected','Disconnected','Waiting For Load'];
  public doughnutChartOptions: any = {cutoutPercentage: 65};

  public unSub_locationHistory=null;
  public ShowDesktopType:any='';


  constructor(private _store:Store<any>, public localStorageService: LocalStorageService) {

    this.unSub_locationHistory = _store.select('vehicleForLocationHistory').subscribe(value => {
      if(value == null)
      {
        console.log('No vehicle selected');
        return;
      }
      else{
        if(value['vts']== 'WaitingForLoad'){
          this.ShowDesktopType='locationHis';
        }
        else{
          this.ShowDesktopType = '';
          setTimeout(()=> {
            this.ShowDesktopType = 'tripDesk';
          },10);
        }
      }


      this._store.dispatch({type: "VEHICLE_FOR_LOCATION_HISTORY", payload: null});
    });
  }

  ngOnInit() {
  }

  ngOnDestroy()
  {
    if (typeof this.unSub_locationHistory != 'undefined' && this.unSub_locationHistory != null && this.unSub_locationHistory != undefined){
      console.log("unsusShowLocationHistory");
      this.unSub_locationHistory.unsubscribe();
    }
  }


  filterTripList(status)
  {
    console.log(status);
  }

  chartClicked(event)
  {
    console.log(event);
  }

  closeDetails(){
    this.ShowDesktopType = '';
  }


}



