import { Component, OnInit } from '@angular/core';
import {Store} from "@ngrx/store";
import {go} from "@ngrx/router-store";

import { TataFleetmanService } from '../../../services/tata-fleetman-service';
import { paths } from 'environments/environment.prod';


@Component({
  selector: 'app-tpi-identified-vehicles',
  templateUrl: './tpi-identified-vehicles.component.html',
  styleUrls: ['./tpi-identified-vehicles.component.css']
})
export class TpiIdentifiedVehiclesComponent implements OnInit {
  
 // vehicleRegNo : string;
  isChecked = false;
  public fleetmanList : any;
  public jobId : String ;
  public unSub_tpiVehicleList = null;
  public unSub_scheduleJobEventEmitter = null;
  public unSub_etransVehicleList = null;
  public unSub_trackingExpertLogin = null;
  public obj;
  //selectedAll: any;

  public view ='';
  
  ngOnDestroy() {
    
    if (typeof this.unSub_tpiVehicleList != 'undefined' && this.unSub_tpiVehicleList != null && this.unSub_tpiVehicleList != undefined){
      console.log("unSub tpi VehicleList");
      this.unSub_tpiVehicleList.unsubscribe();
    }

    if (typeof this.unSub_scheduleJobEventEmitter != 'undefined' && this.unSub_scheduleJobEventEmitter != null && this.unSub_scheduleJobEventEmitter != undefined){
      console.log("unSub schedule job Event Emitter");
      this.unSub_scheduleJobEventEmitter.unsubscribe();
    }

    if (typeof this.unSub_etransVehicleList != 'undefined' && this.unSub_etransVehicleList != null && this.unSub_etransVehicleList != undefined){
      console.log("unSub Etrans VehicleList");
      this.unSub_etransVehicleList.unsubscribe();
    }

    if (typeof this.unSub_trackingExpertLogin != 'undefined' && this.unSub_trackingExpertLogin != null && this.unSub_trackingExpertLogin != undefined){
      console.log("unSub Tracking Expert Login");
      this.unSub_trackingExpertLogin.unsubscribe();
    }
  
  }

   constructor(private _store:Store<any>, private tataFleetmanService:TataFleetmanService ) { 
        this.view=document['view'];

        this.unSub_tpiVehicleList = _store.select('tpiVehicleList').subscribe(value => {
          console.log('vehicle data object:')
          console.log(value);
          
          if (value == null) {
           
            return;
          }

          if (value['error'] == '') {
            this.fleetmanList = value['res']['vehicleList'];
            this.jobId = value['res']['jobId'];
           console.log(this.fleetmanList);
       
          } else {
            console.log(value['errorMsg']);
         
          }
          _store.dispatch({type: "TPI_VEHICLE_LIST",payload:null})
        });

        this.unSub_etransVehicleList = _store.select('etransVehicleList').subscribe(value => {
          console.log('vehicle Etrans data object:')
          console.log(value);
          
          if (value == null) {
           
            return;
          }
    
          if (value['error'] == '') {
            this.fleetmanList = value['res']['vehicleList'];
            this.jobId = value['res']['jobId'];
           console.log(this.fleetmanList);

          } else {
            console.log(value['errorMsg']);
           
          }
          _store.dispatch({type: "ETRANS_VEHICLE_LIST",payload:null})
        });

        //************************************************************************************ */
        this.unSub_scheduleJobEventEmitter = _store.select('scheduleJobEventEmitter').subscribe(value => {
          console.log(value);
          if (value == null) {
            return;
          }
          if(value['error'] == ''){
            console.log('vehicle data is available:')
            this.fleetmanList = value['res'];
            console.log(this.fleetmanList);
            
            this._store.dispatch(go(['/vtsdashboard/thirdPartyIntegration']));
          }
          _store.dispatch({type: "SCHEDULE_JOB_EVENT_EMITTER",payload:null})

      });


      this.unSub_trackingExpertLogin = _store.select('trackingExpertLogin').subscribe(value => {
        console.log('vehicle Tracking expert login :')
        console.log(value);
        
        if (value == null) {
          return;
        }
  
        if (value['error'] == '') {
          this.fleetmanList = value['res']['vehicleList'];
          this.jobId = value['res']['jobId'];
         console.log(this.fleetmanList);

        } else {
          console.log(value['errorMsg']);
         ;
        }
        _store.dispatch({type: "TRACKING_EXPERT_LOGIN",payload:null})
      });
    
  }
  
  ngOnInit() {
  }

  goToDone(){
  
    let seletedVehicles = [];
    
    for(let i = 0 ; i < this.fleetmanList.length ; i ++){
      //console.log(this.fleetmanList[i].isChecked)
      if(this.fleetmanList[i].isChecked){
        seletedVehicles.push(this.fleetmanList[i]);
        
      }
      
    }
    let dataObj ={
      "jobId":this.jobId,
	    "vehicleList":seletedVehicles,
    }
    console.log(dataObj);
    this.tataFleetmanService.getScheduleJobEventEmitter.emit(dataObj);

  }

  goBack(){
    this._store.dispatch(go(['/vtsdashboard/thirdPartyIntegration']));
  }

  selectAll(){
    for(let i = 0 ; i < this.fleetmanList.length ; i ++){
      this.fleetmanList[i].isChecked = this.isChecked;
    }
  }
  checkIfAllSelected() {
    this.isChecked = this.fleetmanList.every(function(item:any) {
        return item.selected == true;
      })
  }

}



