import { Component, OnInit } from '@angular/core';
import {Store} from "@ngrx/store";
import {go} from "@ngrx/router-store";
import {VehicleManagementService} from "../../../services/vehicle-management-services";
import {title} from "../../../reducers/info_store";
import { DriverManagementService } from "../../../services/driver-registry-service";
import { isNullOrUndefined } from "util";
import { MdSnackBar, MdSnackBarConfig } from "@angular/material";



@Component({
  selector: 'app-edit-vehicles',
  templateUrl: './edit-vehicles.component.html',
  styleUrls: ['./edit-vehicles.component.css']
})
export class EditVehiclesComponent implements OnInit {

  public view ='';
  public allDriverList:any[]=[]
  public vehicleTypeList:any[] =[
    'Tata 407 2.5MT',
    'Canter/Swaraj Mazda 3.5MT',
    'LPT 709 4.5MT',
    'LPT 1109 7.5MT',
    'Taurus 15MT',
    'Containerized Vehicle 7 MT',
    'Containerized Vehicle 14 MT',
    'Open Truck 9MT',
    'JCB 8MT',
    'High Bed Trailer Double Axle Upto 22 MT',
    'Trailer Triple Axle Upto 27 MT',
    'Horse for Mobile Unit upto 49 MT',
    'Low Bed Trailer for ODC Upto 49 MT',
    'Volvo/Mann/4923/4921 49 MT',
    'Volvo/Mann/AMW 150 MT',
    'boxTruck'
  ];
  public vehicleStatusList:any[] =["Own Vehicle","Partner Vehicle"];
  public vehicleNumber:string ='';
  public deviceImei:string ='';
  public _calledFrom: any;
  public vehicleType:string ='';
  public groupId:string ='';
  public orgId:string ='';
  public vehicleStatus:string ='';
  public selectedVehicle: any = {};
  public driverName:any=null;
  public mobNo:any=null;
  public driverId:any=null;
  public isShowDriver:boolean=false;
  public isRemoveIcon:boolean=false;
  public isGod:boolean=false;


  private unSub_SelectedVehicle: any = null;
  private unSub_editVehicleResponse: any = null;
  private unSub_deleteVehicleResponse: any = null;
  private unSub_calledFromSource: any = null;
  private unSub_SelectedGroup: any = null;
  private unSub_allDeviceList:any=null;
  private unSub_customerDetails:any=null;
  private unSub_removeDriver:any=null;
  public isShowAddDriver:boolean=false;

  constructor(private _store:Store<any>,private driverService:DriverManagementService, public vehicleMgmntService: VehicleManagementService,public snackBar: MdSnackBar) {
    this.view=document['view'];
    this._store.dispatch({type: "UPDATE_VEHICLE_RESPONSE", payload:null});
    
    // this._store.dispatch({type: "TITLE",payload:'Edit Vehicle'});// set the header name

    this.unSub_SelectedVehicle = _store.select('selectedVehicleForEdit').subscribe(value => {

      if (value == null) {
        return;
      }
      console.log('selected Vehicle For Edit');
      console.log(value);
      this.selectedVehicle=value;
      this.vehicleNumber = value['vehicleRegistrationNumber'];
      this.vehicleType = value['vehicleType'];
      this.deviceImei=value['vtsDeviceId'];
      if(value['driver']!=null){
        this.isRemoveIcon=true;
        this.driverName=value['driver']['name'];
        this.mobNo=value['driver']['mobileNumber'];
        this.driverId=value['driver']['uuid'];
      }
      this.driverService.getAllDriverList.emit();
    });

    this.unSub_customerDetails = _store.select('customerDetails').subscribe(value => {
      console.log(value);
      // this.allUsersList=[];
      if(value==null){
        return;
      }

      this.orgId=value['customer']['orgId'];
      if(value['customer']['isGod'] == true){
        this.isGod=true;
      }

    });

    this.unSub_SelectedGroup = _store.select('selectedVehicleGroupForEdit').subscribe(value => {

      if (value == null) {
        return;
      }
      console.log('selected Vehicle Group For Edit in component');
      console.log(value);
      this.groupId=value['uuid'];
    });

    this.unSub_editVehicleResponse = _store.select('updateVehicleResponse').subscribe(value => {

      console.log(value);
      if (value == null) {
        return;
      }
      if(value['status']==200){
        this.openSnackBar("Vehicle Updated successfully");
      }
      else
      this.openSnackBar("Unable to Update vehicle,try again")
      console.log("EDIT selected vehicle");

      this.goToManageVehicles();

    });

    this.unSub_deleteVehicleResponse = _store.select('deleteVehiclesFromGroup').subscribe(value => {

      if (value == null) {
        return;
      }

      console.log("Delete selected vehicle");
      console.log(value);

      this.goToManageVehicles();
    });

    this.unSub_calledFromSource = _store.select('calledEditVehicleSource').subscribe(value => {

      if (value == null) {
        return;
      }
      console.log(value);
       this._calledFrom=value;

    });

    this.unSub_allDeviceList = _store.select('allDriverList').subscribe(value => {
      console.log(value);
      if (value == null) {
        return;
      }
      if(value['error'] =='')
      {
        this.allDriverList=value['data'];
      }
      else {
        this.allDriverList=null;
      }
    });



    // this.unSub_removeDriver = _store.select('removeDriver').subscribe(value => {

    //         if (value == null) {
    //           return;
    //         }

    //         this.openSnackBar("Driver removed");
    //         console.log('Driver removed');
    //         console.log(value);
    //       });

  }


  ngOnInit() {
  }

  ngOnDestroy() {
    if (typeof this.unSub_SelectedVehicle != 'undefined' && this.unSub_SelectedVehicle != null && this.unSub_SelectedVehicle != undefined)
      this.unSub_SelectedVehicle.unsubscribe();

    if (typeof this.unSub_calledFromSource != 'undefined' && this.unSub_calledFromSource != null && this.unSub_calledFromSource != undefined)
      this.unSub_calledFromSource.unsubscribe();

    // if (typeof this.unSub_editVehicleResponse != 'undefined' && this.unSub_editVehicleResponse != null && this.unSub_editVehicleResponse != undefined)
    //   this.unSub_editVehicleResponse.unsubscribe();

    if (typeof this.unSub_deleteVehicleResponse != 'undefined' && this.unSub_deleteVehicleResponse != null && this.unSub_deleteVehicleResponse != undefined)
      this.unSub_deleteVehicleResponse.unsubscribe();

    if (typeof this.unSub_allDeviceList != 'undefined' && this.unSub_allDeviceList != null && this.unSub_allDeviceList != undefined)
      this.unSub_allDeviceList.unsubscribe();

    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined)
      this.unSub_customerDetails.unsubscribe();

      this._store.dispatch({type: "DELETE_VEHICLES_FROM_GROUP_RESPONSE", payload: null});
      // this._store.dispatch({type: "REMOVE_DRIVER_RESPONSE", payload: null});
      

  }



  goToManageVehicles()
  {
    this._store.dispatch({type: "CALLED_EDIT_VEHICLE_SOURCE",payload: "VehicleList"});

    this._store.dispatch({type: "REFRESH_LIST",payload:'Reload Vehicle List'});
    this._store.dispatch(go(['/vtsdashboard/manageVehicles']));
  }


  deleteSelectedVehicleFromGroup()
  {
    console.log(this.selectedVehicle);
    console.log(this.groupId);
    let obj={
      groupId:this.groupId,
      vehicleId :this.selectedVehicle['uuid']
    }

    console.log(this.groupId);
    console.log(obj);
    this.vehicleMgmntService.deleteSelecetedVehicle.emit(obj);
    this.goToManageVehicles();
  }


  saveEditVehicles()
  {
    // if (isNullOrUndefined(this.driverId) || this.driverId == "") {
    // this.vehicleMgmntService.removeDriver.emit({driver:""})
    // }
    let obj: any;

    if(this.isGod) {
      obj = {
        vehicleRegistrationNumber: this.vehicleNumber,
        vehicleType: this.vehicleType,
        uuid: this.selectedVehicle['uuid'],
        driverId: this.driverId,
        vtsDeviceId: this.deviceImei
      }
    }

    if(!this.isGod){
      obj = {
        vehicleRegistrationNumber: this.vehicleNumber,
        vehicleType: this.vehicleType,
        uuid: this.selectedVehicle['uuid'],
        driverId: this.driverId,
      }
    }

    this.vehicleMgmntService.editSelecetedVehicle.emit(obj);

    this.goToManageVehicles();

    console.log(obj);
  }
  removeDriver(){
    this.driverId='isDeleted';
    this.driverName=null;
    this.mobNo=null;
    this.isRemoveIcon=false;
  }

  setDriver(driver){
    console.log(driver);
    this.driverId=driver['uuid'];
    this.driverName=driver['name'];
    this.mobNo=driver['mobileNumber'];
    this.isShowDriver=false;
    this.isRemoveIcon=true;
  }
  addNewDriver(){
    this.isShowAddDriver=true;
  }
  showAddNewDriver(){
    this.isShowAddDriver=false;
  }
  getDriverDitail(driver){
    console.log(driver);
    if(driver != null || driver != undefined){
      this.driverId=driver['uuid'];
      this.driverName=driver['name'];
      this.mobNo=driver['mobileNumber'];
      this.isRemoveIcon=true;
      this.isShowDriver=false;

      }

  }
  openSnackBar(message: string) {
    this.snackBar.open(message, 'OK', <MdSnackBarConfig>{ duration: 4000 });
  }

}



