import { Component, Inject, Injectable, Optional, EventEmitter, Output } from '@angular/core';
import { Http, Headers, URLSearchParams } from '@angular/http';
import { RequestMethod, RequestOptions, RequestOptionsArgs } from '@angular/http';
import { Response, ResponseContentType } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import { Store } from "@ngrx/store";
import { LocalStorageService } from "angular-2-local-storage";
import { paths } from "../../environments/environment";


@Injectable()
export class TataFleetmanService {

  public defaultHeaders: Headers = new Headers();
  public getTpiVehicleList = new EventEmitter();
  public getScheduleJobEventEmitter = new EventEmitter();
  public getEtransVehicleList = new EventEmitter();
  public getIntegratedVehList = new EventEmitter();
  public getUrl = new EventEmitter();
  public deleteIntegratedVeh = new EventEmitter();
  public getTrackingExpertLogin =new EventEmitter();

  // public token="";

  ngOnDestroy() {

  }


  constructor(protected http: Http, private _store: Store<any>, private localStorageService: LocalStorageService) {
    this.defaultHeaders.append('Access-Control-Allow-Origin', '*');
    this.defaultHeaders.append('Access-Control-Allow-Methods', 'GET,POST, OPTIONS, PUT, PATCH, DELETE');

// ******************************************* TATA FLEET MAN******************************
    this.getTpiVehicleList.subscribe((customer) => {
      let path = paths.TPI_VEHICLE_LIST;
      console.log(path);
      this.getAllList(path, customer).subscribe(response => {
        console.log(response);
        if (response.status == 200) {
          this._store.dispatch({ type: "TPI_VEHICLE_LIST", payload: { error: '', res: response } }); // set the header name
        } else {
          this._store.dispatch({ type: "TPI_VEHICLE_LIST", payload: { error: 'X', errorMsg: 'server error' + response.error } });
        }
      }, err => {
        console.log(err);
        this._store.dispatch({ type: "TPI_VEHICLE_LIST", payload: { error: 'X', errorMsg: 'http error' } }); // set the header name
      });
    });


    this.getScheduleJobEventEmitter.subscribe((data) => {
      let path = paths.SCHEDULE_JOB_EVENT_EMITTER;
      console.log(path);
      this.scheduleJob(path, data).subscribe(response => {
        console.log(response);
        if (response.status == 200) {
          this._store.dispatch({ type: "SCHEDULE_JOB_EVENT_EMITTER", payload: { error: '', res: response } }); // set the header name
        } else {
          this._store.dispatch({ type: "SCHEDULE_JOB_EVENT_EMITTER", payload: { error: 'X', errorMsg: 'server error' + response.error } });
        }
      }, err => {
        console.log(err);
        this._store.dispatch({ type: "SCHEDULE_JOB_EVENT_EMITTER", payload: { error: 'X', errorMsg: 'http error' } }); // set the header name
      });
    });

// ****************************************** Integrated Vehcile List ***********************************

    this.getIntegratedVehList.subscribe(() => {
      let path = paths.INTEGRATED_VEH_lIST;
      console.log(path);
      this.getIntegratedList(path).subscribe(response => {
        console.log(response);
        if (response.status >= 200 && response.status <300) {
          this._store.dispatch({ type: "INTEGRATED_VEH_lIST", payload: { error: '', res: response } }); // set the header name
        } else {
          this._store.dispatch({ type: "INTEGRATED_VEH_lIST", payload: { error: 'X', errorMsg: 'server error' + response.error } });
        }
      }, err => {
        console.log(err);
        this._store.dispatch({ type: "INTEGRATED_VEH_lIST", payload: { error: 'X', errorMsg: 'http error' } }); // set the header name
      });
    });

/********************************* ETRANS LIST ************************************** */

    this.getEtransVehicleList.subscribe((id) => {
      let path = paths.ETRANS_VEHICLE_LIST;
      console.log(path);
      this.getEtransList(path, id).subscribe(response => {
        console.log(response);
        if (response.status == 200) {
          this._store.dispatch({ type: "ETRANS_VEHICLE_LIST", payload: { error: '', res: response } }); // set the header name
        } else {
          this._store.dispatch({ type: "ETRANS_VEHICLE_LIST", payload: { error: 'X', errorMsg: 'server error' + response.error } });
        }
      }, err => {
        console.log(err);
        this._store.dispatch({ type: "ETRANS_VEHICLE_LIST", payload: { error: 'X', errorMsg: 'http error' } }); // set the header name
      });
    });
  

// ***************************************** Rooster Info *********************************************
  this.getUrl.subscribe(() => {
    let path = paths.GENERATE_URL;
    console.log(path);
    this.getUrlPath(path).subscribe(response => {
      console.log(response);
      if (response.status == 200) {
        this._store.dispatch({ type: "GENERATE_URL", payload: { error: '', res: response } }); // set the header name
      } else {
        this._store.dispatch({ type: "GENERATE_URL", payload: { error: 'X', errorMsg: 'server error' + response.error } });
      }
    }, err => {
      console.log(err);
      this._store.dispatch({ type: "GENERATE_URL", payload: { error: 'X', errorMsg: 'http error' } }); // set the header name
    });
  });

// ******************************************* Delete Integrated Vehicle list*************************************

  this.deleteIntegratedVeh.subscribe((jobId)=> {
    let path=paths.DELETE_INTEGRATED_VEHICLE+jobId;
    console.log(path);
    this.deleteVeh(jobId,path).subscribe(response =>{
      console.log(response);
      if (response.status >= 200 && response.status <300) {
        this._store.dispatch({ type: "DELETE_INTEGRATED_VEHICLE", payload: { error: '', res: response } }); // set the header name
      } else {
        this._store.dispatch({ type: "DELETE_INTEGRATED_VEHICLE", payload: { error: 'X', errorMsg: 'server error' + response.error } });
      }
    }, err => {
      console.log(err);
      this._store.dispatch({ type: "DELETE_INTEGRATED_VEHICLE", payload: { error: 'X', errorMsg: 'http error' } }); // set the header name
    });
  });
  //     this._store.dispatch({type: "DELETE_INTEGRATED_VEHICLE" ,payload:response});
  //   },err=>{
  //     console.log(err);
  //   });
  // });

  // ******************************************** Tracking Experts Login**********************************************

  this.getTrackingExpertLogin.subscribe((list) => {
    let path = paths.TRACKING_EXPERT_LOGIN;
    console.log(path);
    this.getTrackingLogin(path, list).subscribe(response => {
      console.log(response);
      if (response.status == 200) {
        this._store.dispatch({ type: "TRACKING_EXPERT_LOGIN", payload: { error: '', res: response } }); // set the header name
      } else {
        this._store.dispatch({ type: "TRACKING_EXPERT_LOGIN", payload: { error: 'X', errorMsg: 'server error' + response.error } });
      }
    }, err => {
      console.log(err);
      this._store.dispatch({ type: "TRACKING_EXPERT_LOGIN", payload: { error: 'X', errorMsg: 'http error' } }); // set the header name
    });
  });

  }

  public getAllList(path, customer) {
    let headers = new Headers(this.defaultHeaders.toJSON());
    let token = this.localStorageService.get('AUTH_TOKEN');
    console.log(token);
    let bearerToken = "Bearer " + token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body: customer
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      console.log(response);
      if (response.status == 200) {
        return response.json();
      }

    });

  }


   scheduleJob(path, data) {
    
    let headers = new Headers(this.defaultHeaders.toJSON());
    let token = this.localStorageService.get('AUTH_TOKEN');
    console.log(token);
    let bearerToken = "Bearer " + token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body: data
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      console.log(response);
      if (response.status == 200) {
        return response.json();
      }

    });
  }

//********************************************************************************************* */

getIntegratedList(path) {
    let headers = new Headers(this.defaultHeaders.toJSON());
    let token = this.localStorageService.get('AUTH_TOKEN');
    console.log(token);
    let bearerToken = "Bearer " + token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
      // body: list
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      console.log(response);
      if (response.status == 200) {
        return response.json();
      }

    });

  }

  getEtransList(path, id) {
    let headers = new Headers(this.defaultHeaders.toJSON());
    let token = this.localStorageService.get('AUTH_TOKEN');
    console.log(token);
    let bearerToken = "Bearer " + token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body: id
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      console.log(response);
      if (response.status == 200) {
        return response.json();
      }

    });

  }

  getUrlPath(path) {
    let headers = new Headers(this.defaultHeaders.toJSON());
    let token = this.localStorageService.get('AUTH_TOKEN');
    console.log(token);
    let bearerToken = "Bearer " + token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
    //  body: url
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      console.log(response);
      if (response.status == 200) {
        return response.json();
      }

    });

  }

  deleteVeh(jobId,path){
    let headers = new Headers(this.defaultHeaders.toJSON());
    let token = this.localStorageService.get('AUTH_TOKEN');
    let bearerToken="Bearer "+ token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body: jobId,
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      console.log(response);
      if(response.status ==200) {
        return response.json();
      }

    });
  }

  getTrackingLogin(path, list) {
    let headers = new Headers(this.defaultHeaders.toJSON());
    let token = this.localStorageService.get('AUTH_TOKEN');
    console.log(token);
    let bearerToken = "Bearer " + token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body: list
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      console.log(response);
      if (response.status == 200) {
        return response.json();
      }

    });

  }

}



