import {ActionReducer, Action, combineReducers} from '@ngrx/store';
import {ActionRecorderServices} from "../services/actionRecorder.service";
var obj=new ActionRecorderServices();


export function allDriverList(state: any = null, action:Action) {
  switch (action.type) {
    case "ALL_DRIVER_LIST" : {
      obj.addAction(action);
      return action.payload;
    }
    default:
      return state;
  }
}
export function driverCommand(state: any = null, action:Action) {
    switch (action.type) {
      case "DRIVER_COMMAND" : {
        obj.addAction(action);
        return action.payload;
      }
      default:
        return state;
    }
  }
  export function addDriver(state: any = null, action:Action) {
    switch (action.type) {
      case "ADD_DRIVER" : {
        obj.addAction(action);
        return action.payload;
      }
      default:
        return state;
    }
  }


