import { ActionReducer, Action } from '@ngrx/store';
import {ActionRecorderServices } from '../services/actionRecorder.service';

var obj=new ActionRecorderServices();

export function unifiedTripStatusList(state: any = null, action:Action) {

  switch (action.type) {
    case "UNIFIED_TRIP_STATUS_LIST" : {
      obj.addAction(action);
      return action.payload;
    }

    default:
      return state;
  }
}

export function unifiedVehicleTripDetails(state: any = null, action:Action) {

  switch (action.type) {
    case "UNIFIED_VEHICLE_TRIP_DETAILS" : {
      obj.addAction(action);
      return action.payload;
    }

    default:
      return state;
  }

}



