import {Component, Input, OnInit, Pipe, PipeTransform} from '@angular/core';
import {FormControl} from '@angular/forms';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/operator/map';
import {map} from 'rxjs/operator/map';
import {Store} from '@ngrx/store';
import {Services} from '../../services/services';
import {VehicleManagementService} from "../../services/vehicle-management-services";



@Component({
  selector: 'app-vehicle-picker-autocomplete',
  templateUrl: './vehicle-picker-autocomplete.component.html',
  styleUrls: ['./vehicle-picker-autocomplete.component.css']
})
export class VehiclePickerAutocompleteComponent implements OnInit {

  public vehcileControl : FormControl = new FormControl();

  public filteredList: Observable<any[]>;
  public selectedVehicle:any ="";
  public selectedValues:any=[];
  public selectedChipList:any=[];
  public selectedVehicles:any=[];
  public vehicleGroupList:any=[];


  public _vehiclesForList:any=[];
  public _groupsForList:any=[];

  public orgId='';
  public errorMsg='';
  public unSub_vehicleList=null;
  public unSub_groupList=null;
  public unSub_customerDetails:any=null;

  public vehicles: any=[];


  @Input() editableVehicleList:any=[];

  constructor(private _store:Store<any>,private _services:Services,private _vehServices:VehicleManagementService) {
    console.log("constructor");


    let customer={};

    this.unSub_customerDetails = _store.select('customerDetails').subscribe(value => {
      if(value==null)
        return;

      if(value['error']==''){
        customer= value['customer'];
        console.log(customer);
      }else{
        return;
      }

      this.orgId=customer['orgId'];


      // ***************  vehicle list ************************************
      this.unSub_vehicleList = _store.select('vehicleListRes').subscribe(value => {
        console.log(value);
        this.errorMsg='';
        // check not  exist then request for list
        if(value==null){
          _services.getVehicleList.emit(this.orgId);
          return;
        }

        if(value['error']==''){
          this.vehicles = value['data'];
          console.log(this.vehicles);

          let allVehList=[];


          for(let i=0;i<this.vehicles.length;i++){
            let veh=[];
            veh.push(this.vehicles[i]);
            allVehList.push(this.vehicles[i]);

            let obj={
              _vehicle: veh,
              _type: "vehicle",
              _name: this.vehicles[i].vehicleRegistrationNumber
            }

            this._vehiclesForList.push(obj);
          }

          let allObj={
            _vehicle: allVehList,
            _type: "all",
            _name: 'ALL VEHICLES'
          }

          this._vehiclesForList.push(allObj);

        }else {
          console.log(value['errorMsg']);
          this.errorMsg=value['errorMsg'];
        }

        // this.selectedVehiclesForEdit();

        console.log("this._vehiclesForList");
        console.log(this._vehiclesForList);


      });

      this.unSub_groupList = _store.select('getAllVehiclesGroupList').subscribe(value => {

        if(value==null)
        {
          _vehServices.getAllVehicleGroupsList.emit(this.orgId);
          return;
        }
        console.log('all vehicle group list in component');
        console.log(value);
        if(value['status']==200)
        {
          this.vehicleGroupList=value['list'];

          let vehList=[];

          for(let i=0; i<this.vehicleGroupList.length;i++)
          {
            console.log(this.vehicleGroupList[i]['uuid']);
            vehList=[];
            for(let k=0; k<this.vehicles.length;k++){
              if(this.vehicles[k]['groups']== null || this.vehicles[k]['groups']==undefined)
              {
                return;
              }

              if(this.vehicles[k]['groups'].indexOf(this.vehicleGroupList[i]['uuid']) != -1){
                console.log("matched");
                vehList.push(this.vehicles[k]);
                break;
              }
            }
            let obj={
              _vehicle:vehList,
              _type:"group",
              _name:this.vehicleGroupList[i]['groupName']
            }

            this._groupsForList.push(obj);
            this._vehiclesForList.push(obj);
          }
        }

        console.log(this._vehiclesForList);
      });

    });

  }

  ngOnInit() {

    // this.filteredList=this.vehicles;
    //     this.filteredList = this.vehcileControl.valueChanges.pipe(
    //   startWith(''),
    //   map(vehicle => this.filteredVehList(vehicle)
    // );

    this.selectedVehiclesForEdit();
    this.filteredList = this.vehcileControl.valueChanges.map(val => this.filteredVehList(val));
  }


  ngOnDestroy()
  {
    console.log("vehicle picker autocomplete destroy");
    // this._store.dispatch({type: "PICKED_VEHICLES",payload:[]});
    // if (typeof this.unSub_vehicleList != 'undefined' && this.unSub_vehicleList != null && this.unSub_vehicleList != undefined)
    //   this.unSub_vehicleList.unsubscribe();

    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined)
      this.unSub_customerDetails.unsubscribe();


    console.log(this.editableVehicleList);
    console.log(this.selectedChipList);
  }

  filteredVehList(vehicle) :any[]
  {
    // return this.vehicles.filter(vehi =>
    //   vehi.vehicleRegistrationNumber.toLowerCase().indexOf(vehicle.toLowerCase()) === 0);

    return this._vehiclesForList.filter(vehi =>
      vehi._name.toLowerCase().indexOf(vehicle.toLowerCase()) === 0);
  }

  makeChip(event, inputValue)
  {
    console.log(event);
    console.log(inputValue);

    if(event.keyCode== 13 ||event.keyCode== 32 || event.keyCode== 186 || event.keyCode== 188 || event.type== "blur")
    {
      // let value= inputValue.toString().trim();
      let value= inputValue.toString().toUpperCase();
      let selectedVal={};

      if(event.keyCode!= 13) {
        value = value.substring(0, value.length - 1);
      }
      let valid=false;

      for(let k=0 ;k<this._vehiclesForList.length; k++){
        if(this._vehiclesForList[k]['_name'].toString().toUpperCase()== value)
        {
          valid=true;
          selectedVal=this._vehiclesForList[k];
          break;

        }
      }

      if(valid){
        console.log("A valid value");

        if(this.selectedChipList.indexOf(value) == -1) {
          this.selectedChipList.push(value);
        }
        this.selectVehicleFromValue(selectedVal);
        this.selectedVehicle="";

      }
      if(!valid)
      {
        console.log("An invalid value");
      }
    }

  }

  selectVehicleFromValue(value)
  {
    console.log(value);

    let _val=value['_vehicle'];
    console.log(_val);

    for(let i=0; i< _val.length; i++)
    {
      if(this.selectedValues.indexOf(_val[i]) == -1) {
        this.selectedValues.push(_val[i]);
      }
    }

    console.log(this.selectedValues);
    let arrayStr=JSON.stringify(this.selectedValues);
    let selval=[];

    let arr=arrayStr=JSON.parse(arrayStr);

    for(let i=0; i<arr.length; i++)
    {
      selval.push(arr[i]);
    }

    console.log(selval);

    this._store.dispatch({type: "PICKED_VEHICLES",payload:selval});
  }


  remove(i,valueName){
    console.log("remove from list "+ i);

    this.selectedChipList.splice(i,1);

    let obj:any;

    for(let k=0 ;k<this._vehiclesForList.length; k++){
      if(this._vehiclesForList[k]['_name']== valueName)
      {
        obj=this._vehiclesForList[k];
        this.removeValue(obj);
        break;
      }
    }

    console.log(obj);
  }


  removeValue(vehicleListObj)
  {
    let vehList=vehicleListObj['_vehicle'];
    console.log(vehList);
    for(let i=0; i< vehList.length; i++)
    {
      this.selectedValues.splice(this.selectedValues.indexOf(vehList[i]),1);
    }

    console.log(this.selectedValues);

    let arrayStr=JSON.stringify(this.selectedValues);
    let selval=[];

    let arr=arrayStr=JSON.parse(arrayStr);

    for(let i=0; i<arr.length; i++)
    {
      selval.push(arr[i]);
    }

    console.log(selval);

    this._store.dispatch({type: "PICKED_VEHICLES",payload:selval});
  }

  selectedVehiclesForEdit()
  {
    console.log("Selected Vehcile for edit");
    console.log(this.editableVehicleList);

    if((this.editableVehicleList.length > 0) && (this.vehicles.length > 0))
    {
      console.log(this.editableVehicleList.length);
      console.log(this.vehicles.length);

      let selectedList=[];
      for(let i=0; i<this.editableVehicleList.length; i++)
      {
        for(let k=0; k<this.vehicles.length; k++)
        {
          if(this.vehicles[k]['uuid'] == this.editableVehicleList[i])
          {
            console.log("matched for create chip");
            selectedList.push(this.vehicles[k]);
            this.selectedChipList.push(this.vehicles[k]['vehicleRegistrationNumber']);
            break;
          }
        }
      }

      // this.selectedVehicles=selectedList;
      this.selectedValues=selectedList;
      console.log(this.selectedValues);

      let arrayStr=JSON.stringify(this.selectedValues);
      let selval=[];

      let arr=arrayStr=JSON.parse(arrayStr);

      for(let i=0; i<arr.length; i++)
      {
        selval.push(arr[i]);
      }

      console.log(selval);
      console.log(this.selectedChipList);


      this._store.dispatch({type: "PICKED_VEHICLES",payload:selval});
    }

  }

}



