import { Component, OnInit } from '@angular/core';
import {Store} from "@ngrx/store";
import {go} from "@ngrx/router-store";

import { TataFleetmanService } from '../../../services/tata-fleetman-service';
import { paths } from 'environments/environment.prod';
import {MdSnackBar, MdSnackBarConfig} from "@angular/material";

@Component({
  selector: 'app-tracking-experts',
  templateUrl: './tracking-experts.component.html',
  styleUrls: ['./tracking-experts.component.css']
})
export class TrackingExpertsComponent implements OnInit {

  public unSub_trackingExpertLogin = null;
  public view ='';
  public isSpinner= false;
  public obj ={
    "url": "",
  }
  ngOnDestroy() {
    
    if (typeof this.unSub_trackingExpertLogin != 'undefined' && this.unSub_trackingExpertLogin != null && this.unSub_trackingExpertLogin != undefined){
      console.log("unSub Tracking Expert Login");
      this.unSub_trackingExpertLogin.unsubscribe();
    }
  }

  constructor(private _store:Store<any>,private tataFleetmanService:TataFleetmanService, public snackBar: MdSnackBar) {
    this.view=document['view'];

    this.unSub_trackingExpertLogin = _store.select('trackingExpertLogin').subscribe(value => {
      //console.log(value);
      if(value==null)
      {
        return;
      }
      
      if(value['error'] == '') {
        this.isSpinner=false;
        console.log(value);     
        this._store.dispatch(go(['/vtsdashboard/tpiIdentifiedVehicles']));
      }
        else {
          this.isSpinner=false;
          this.openSnackBar(" UNAUTHORISED ACCESS ");
          console.log(value['errorMsg']);
        
      }
    });
   }

  ngOnInit() {
  }
  goToCancel(){
    this._store.dispatch(go(['/vtsdashboard/thirdPartyIntegration']));
  }
  goToSubmitUrl(){
      if(this.obj.url == '' || this.obj.url ==undefined){
        console.log("please input URL");
        this.openSnackBar("Please input URL");
        return false;
      }
      else {
        this.isSpinner= true;
        this.tataFleetmanService.getTrackingExpertLogin.emit(this.obj);
        }
  }


  openSnackBar(message: string) {
    this.snackBar.open(message , '', <MdSnackBarConfig>{duration :  3000});
   }

}




