import { Component, OnInit } from '@angular/core';
import {Store} from "@ngrx/store";
import {go} from "@ngrx/router-store";

@Component({
  selector: 'app-tpi-list',
  templateUrl: './tpi-list.component.html',
  styleUrls: ['./tpi-list.component.css']
})
export class TpiListComponent implements OnInit {

  id: number;
  tpiList: string ;
  public thirdPartyList:any = [];

  constructor(private _store:Store<any>) { 
    this.thirdPartyList = [
      { id: 1 , tpiList: "Tata Fleet Man"},
      { id: 2 , tpiList: "E Trans"},
      { id: 3 , tpiList: "Rooster Info"},
      { id: 4 , tpiList: "Tracking Experts"}
    ]
  }

  ngOnInit() {
  }

  goToTataFleetmanForm(tpiObj){
    if(tpiObj.id == 1){
      
      this._store.dispatch(go(['/vtsdashboard/tataFleetmanForm']));
    }
    else if(tpiObj.id == 2){
      this._store.dispatch(go(['/vtsdashboard/tpiEtransForm']));
    }
    else if(tpiObj.id == 3){
      this._store.dispatch(go(['/vtsdashboard/roosterInfo']));
    }
    else if(tpiObj.id == 4){
      this._store.dispatch(go(['/vtsdashboard/trackingExperts']));
    }

  }

}



