import { Component, OnInit } from '@angular/core';
import {Store} from "@ngrx/store";
import {go} from "@ngrx/router-store";

import { TataFleetmanService } from '../../services/tata-fleetman-service';
import { paths } from 'environments/environment.prod';
import {MdSnackBar, MdSnackBarConfig} from "@angular/material";

@Component({
  selector: 'app-third-party-integration',
  templateUrl: './third-party-integration.component.html',
  styleUrls: ['./third-party-integration.component.css']
})
export class ThirdPartyIntegrationComponent implements OnInit {
  
  public view ='';
 
  public vehObjForDelete:any={};

  public unSub_integratedVehList = null;
  public unSub_deleteIntegratedVehicles = null;
  public isShowConfirmDialogue:boolean=false;
  public integratedList : any;
  public obj ={
    "response": [{
        "totalVehicles":[],
        "type": "",
        "status" : "",
    }],
    "status":"",
  }

  ngOnDestroy() {
    
    if (typeof this.unSub_integratedVehList != 'undefined' && this.unSub_integratedVehList != null && this.unSub_integratedVehList != undefined){
      console.log("unSub Integrated Vehicle List");
      this.unSub_integratedVehList.unsubscribe();
    }

    if (typeof this.unSub_deleteIntegratedVehicles != 'undefined' && this.unSub_deleteIntegratedVehicles != null && this.unSub_deleteIntegratedVehicles != undefined){ this.unSub_deleteIntegratedVehicles.unsubscribe();
      console.log("unSub delete Integrated Vehicle List");
    }
  }

  constructor(private _store:Store<any>, private tataFleetmanService:TataFleetmanService,public snackBar: MdSnackBar) { 
    this.view=document['view'];
   // this._store.dispatch({type: "TITLE",payload:'Third Party Integration'});// set the header name

    //this.view=document['view'];
  
    this.unSub_integratedVehList = _store.select('integratedVehList').subscribe(value => {
      //console.log(value);
      if(value==null)
      {
        return;
      }
      
      console.log(typeof value['res']['response'] =='string');
      if(value['error'] == '' ) {
        if(typeof value['res']['response'] =='string'){
          this.integratedList = JSON.parse(value['res']['response']);
        }else{
          this.integratedList = value['res']['response'];
        }
       
        console.log(this.integratedList); 
      }
        else {
          console.log(value['errorMsg']);
        }
     // _store.dispatch({type: "INTEGRATED_VEH_lIST",payload:null})
    });

    this.unSub_deleteIntegratedVehicles = _store.select('deleteIntegratedVehicle').subscribe(value =>{
      if(value==null)
      {
        return;
      }
      console.log('Delete Alert Response');
      console.log(value);


      if(value['error'] =="")
      {
        console.log(value);
        this.tataFleetmanService.getIntegratedVehList.emit();
        this.openSnackBar("Vehicle Deleted Successfully!");
        
      }else{
        this.openSnackBar(value['errorMsg']);
      }
      
       this._store.dispatch({type: "DELETE_INTEGRATED_VEHICLE" ,payload:null});
     
    });
  
  }

  ngOnInit() {
    this.tataFleetmanService.getIntegratedVehList.emit();
     }

  goToTpiList(){
      this._store.dispatch(go(['/vtsdashboard/tpiList']));
    
   }

   showConfirmDialogue(vehJob)
  {
    console.log(vehJob);
    console.log("showing confirm dialogue");
    this.vehObjForDelete=vehJob['jobId'];
    this.isShowConfirmDialogue=true;
  }
  
  deleteSelecetedVeh(){
  
      console.log(this.vehObjForDelete);
       if(this.vehObjForDelete!== {} )
          this.isShowConfirmDialogue=false;
    this.tataFleetmanService.deleteIntegratedVeh.emit(this.vehObjForDelete);
    
  }
  openSnackBar(message: string) {
    this.snackBar.open(message , '', <MdSnackBarConfig>{duration :  3000});
  }

}



